<template>
  <div class="contents_div_1">
    <div class="Menu4 p-3 my-3 border">
      <h6>[Menu4-3.vue]</h6>    
      <Menu4_Nav />
      <li>공공데이터활용지원센터_보건복지부 코로나19 감염 현황</li>
      <li>데이터 : 공공데이터포털 (https://www.data.go.kr/iim/api/selectAPIAcountView.do)</li>
    </div> 
    <div class="row ">
      <table class=".table-sm">
        <thead> 
          <tr>
            <th v-for="(a,i) in arrKey" :key="i" v-text="a"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(row,rid) in arr" :key="rid">
            <td v-for="(col,cid) in row" :key="cid" v-text="col"></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  import Menu4_Nav from './Menu4_Nav';
  import axios from 'axios'

  var moment = require('moment'); // moment 모듈불러오기
  var vStartCreateDt = moment().subtract(2,'months'); //한달 전
  var vEndCreateDt = moment().subtract(1,'months');
  var vStartYMD = new String;
  var vEndYMD = new String;

  // 공공데이터활용지원센터_보건복지부 코로나19 감염 현황
  // var URL = "http://openapi.data.go.kr/openapi/service/rest/Covid19/getCovid19InfStateJson?serviceKey=Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D&pageNo=1&numOfRows=10&startCreateDt=20220801&endCreateDt=20220905";
  var URL = "http://openapi.data.go.kr/openapi/service/rest/Covid19/getCovid19InfStateJson"
  var queryParams = '?' + encodeURIComponent('serviceKey') + '=Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D'; /* Service Key*/
  queryParams += '&' + encodeURIComponent('pageNo') + '=' + encodeURIComponent('1'); /* */
  queryParams += '&' + encodeURIComponent('numOfRows') + '=' + encodeURIComponent('10'); /* */
  queryParams += '&' + encodeURIComponent('startCreateDt') + '=' + encodeURIComponent(vStartYMD); /* */
  queryParams += '&' + encodeURIComponent('endCreateDt') + '=' + encodeURIComponent(vEndYMD); /* */
  URL += queryParams

	// 기상청_생활기상지수 조회서비스(3.0) 
  // XML  http://apis.data.go.kr/1360000/LivingWthrIdxServiceV2/getUVIdxV2?ServiceKey=Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D&pageNo=1&numOfRows=10&dataType=XML&areaNo=1111051500&time=2022090310
  // json http://apis.data.go.kr/1360000/LivingWthrIdxServiceV2/getUVIdxV2?ServiceKey=Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D&pageNo=1&numOfRows=10&dataType=JSON&areaNo=1111051500&time=2022090310
  
  export default {
    components: {
              Menu4_Nav: Menu4_Nav, 
          },
    data() { 
              return {
                arr: [],
                arrKey: [],
                vStartYMD: this.vStartYMD, 
                vEndYMD: this.vEndYMD
            }
          },
    mounted () { 
      this.vStartYMD = String(vStartCreateDt.format('YYYYMMDD'));
      this.vEndYMD = String(vEndCreateDt.format('YYYYMMDD'));
      
      console.log(this.vStartYMD);
      console.log(this.vEndYMD);
    },
    created() {
      axios.get(URL)
      .then(response => {
          this.arr = response.data.response.body.items.item
          // console.log(Object.keys(this.arr[0]));

          this.arrKey = Object.keys(this.arr[0]);
          // console.log(this.arrKey);
              })
      .catch(error => {
        console.log(error)
      })

    },
  }
</script>
