<template>
    <p>{{ nowTime }}</p>
    <label for="start">Start date:</label>
    <!-- <input type="date" id="start" name="trip-start" v-model="regDate" value="2023-01-23" min="2022-01-01" max="2031-12-31"> -->
    <input type="date" id="start" name="trip-start" v-model="regDate" min="2022-01-01" max="2031-12-31">
    <input type="time" id="start" name="trip-start" v-model="regDate" min="2022-01-01" max="2031-12-31">
    <br>
    {{ Date() }} <br>
    {{ regDate }}<br>
    <!-- {{ this.date.getFullYear() }}-{{ this.date.getMonth()+1 }}-{{ this.date.getDate() }} <br> -->
    <!-- {{date.getFullYear()}}-{{date.getMonth()+1}}-{{date.getDate()}} -->
    <!-- -${date.getMonth() + 1}-${date.getDate()} -->
    <input type="button" value="저장" @click="regDateFunc();">
</template>
<script>
export default {
  components: {
  },
  data() {
    return {
    //   regDate: '2023-01-23', 
    //   regDate: Date(), 
      date : new Date(),
      regDate: null, 
    };
  },
  created() {
    this.regDate = this.date.getFullYear() + '-' + this.date.getMonth()+1 + '-' + this.date.getDate();
    console.log('default regDate : ', this.regDate);


    
    this.nowTimes();
    this.setDate()
    setInterval(this.nowTimes.bind(this) , 1000);

  },
  mounted() {
  },
  methods: {
    regDateFunc(){
        console.log('selected regDate : ', this.regDate);
    }, 




    
    setDate(){
      let year = new Date().getFullYear();
      let month =new Date().getMonth() + 1 < 10? "0" + (new Date().getMonth() + 1): new Date().getMonth() + 1;
      let date =new Date().getDate() < 10? "0" + new Date().getDate(): new Date().getDate();
      let hh =new Date().getHours() < 10? "0" + new Date().getHours(): new Date().getHours();
      let mm =new Date().getMinutes() < 10? "0" + new Date().getMinutes(): new Date().getMinutes();
      let ss =new Date().getSeconds() < 10? "0" + new Date().getSeconds(): new Date().getSeconds();
      
      return {
             'year' : year
             , 'month' : month
             , 'date' : date
             , 'hh' : hh
             , 'mm' : mm
             , 'ss' : ss
      }
    },
    nowTimes() {
            this.nowTime = this.setDate().year + "-" + this.setDate().month + "-" + this.setDate().date + "-" +
            this.setDate().hh + ":" + this.setDate().mm + ":" + this.setDate().ss;
    }


  },
};
</script>