      <div class="boxes-small" v-for="item in submenu" v-bind:key="item.id" >
        <button class="btn-gradient blue" v-on:click="callApi(item.apiUrl)">{{ item.names }}</button>
      </div>
  </div>
  <div class="container-row">  
    <table>
      <thead>
        <tr>
          <th v-for="(a, i) in arrKey" :key="i" v-text="a"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(row, rid) in arr" :key="rid">
          <td v-for="(col, cid) in row" :key="cid" v-text="col"></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Menu7_Nav from "./Menu7_Nav";
import axios from "axios";
var moment = require("moment"); // moment 모듈불러오기
var vStartCreateDt = moment().subtract(6, "months"); //
var vEndCreateDt = moment().subtract(0, "months");
var vStartYMD = String(vStartCreateDt.format("YYYYMMDD"));
var vEndYMD = String(vEndCreateDt.format("YYYYMMDD"));
var vStartYM = String(vStartCreateDt.format("YYYYMM"));
var vEndYM = String(vEndCreateDt.format("YYYYMM"));

var vEndPreMon = moment().subtract(1, "months");
var vEndYMPre = String(vEndPreMon.format("YYYYMM"));

// console.log('vEndYM :: ',vEndYM);

var serviceKey = 'Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D'
// 공공데이터활용지원센터_보건복지부 코로나19 감염 현황
// var url1 = "http://openapi.data.go.kr/openapi/service/rest/Covid19/getCovid19InfStateJson"
var url1 = "/openapi/service/rest/Covid19/getCovid19InfStateJson";
url1 += "?" + encodeURIComponent("serviceKey") + "=" + serviceKey; /* Service Key*/
url1 += "&" + encodeURIComponent("pageNo") + "=" + encodeURIComponent("1"); /* */
url1 += "&" + encodeURIComponent("numOfRows") + "=" + encodeURIComponent("10"); /* */
url1 += "&" + encodeURIComponent("startCreateDt") + "=" + encodeURIComponent(vStartYMD); /* */
url1 += "&" + encodeURIComponent("endCreateDt") + "=" + encodeURIComponent(vEndYMD); /* */
    
// 한국지역난방공사 전기생산량 정보
// XML / json 
// var url2 = "https://www.kdhc.co.kr:443/openapi-data/service/kdhcPowerProd/powerProd";
// var url2 = 'openapi-data/service/kdhcPowerProd/powerProd?startDate=201008&endDate=202209&pageNo=1&numOfRows=10&serviceKey=Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D'
var url2 = '/openapi-data/service/kdhcPowerProd/powerProd'
url2 += "?" + encodeURIComponent("serviceKey") + "=" + serviceKey; /* Service Key*/
url2 += "&" + encodeURIComponent("pageNo") + "=" + encodeURIComponent("1"); /* */
url2 += "&" + encodeURIComponent("numOfRows") + "=" + encodeURIComponent("10"); /* */
url2 += "&" + encodeURIComponent("startDate") + "=" + encodeURIComponent(vStartYM); /* */
url2 += "&" + encodeURIComponent("endDate") + "=" + encodeURIComponent(vEndYM); /* */
    
// "OpenAPI_ToolInstallPackage/service/rest/RTMSOBJSvc/getRTMSDataSvcLandTrade?LAWD_CD=11110&DEAL_YMD=202208&servicekey=Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D";
// var url3 = 'OpenAPI_ToolInstallPackage/service/rest/RTMSOBJSvc/getRTMSDataSvcLandTrade?serviceKey=Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D&LAWD_CD=11110&DEAL_YMD=202208'
var url3 = '/OpenAPI_ToolInstallPackage/service/rest/RTMSOBJSvc/getRTMSDataSvcLandTrade'
