url3 += "&" + encodeURIComponent("LAWD_CD") + "=" + encodeURIComponent("11110"); /* */
url3 += "&" + encodeURIComponent("DEAL_YMD") + "=" + encodeURIComponent(vEndYMPre); /* */

// var url4 = 'http://openapi.molit.go.kr:8081/OpenAPI_ToolInstallPackage/service/rest/RTMSOBJSvc/getRTMSDataSvcAptTrade?serviceKey=Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D&LAWD_CD=11110&DEAL_YMD=202208';
// var url4 = 'OpenAPI_ToolInstallPackage/service/rest/RTMSOBJSvc/getRTMSDataSvcAptTrade?serviceKey=Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D&LAWD_CD=11110&DEAL_YMD=202208';
var url4 = '/OpenAPI_ToolInstallPackage/service/rest/RTMSOBJSvc/getRTMSDataSvcAptTrade'
url4 += "?" + encodeURIComponent("serviceKey") + "=" + serviceKey; /* Service Key*/
url4 += "&" + encodeURIComponent("LAWD_CD") + "=" + encodeURIComponent("11110"); /* */
url4 += "&" + encodeURIComponent("DEAL_YMD") + "=" + encodeURIComponent(vEndYMPre); /* */

export default {
  components: {
    Menu7_Nav: Menu7_Nav,
  },
  data() {
    return {
      arr: [],
      arrKey: [],
      vStartYMD: this.vStartYMD,
      vEndYMD: this.vEndYMD,
      submenu: [
        { names: "코로나19 감염 현황", apiUrl: url1, id: 1, source: '' },
        { names: "한국지역난방공사 전기생산량 정보", apiUrl: url2, id: 2, source: '' },
        { names: "국토교통부_토지 매매 신고 조회 서비스", apiUrl: url3, id: 3, source: '' },
        // { names: "국토교통부_아파트매매 실거래자료", apiUrl: url4, id: 4, source: 'https://www.data.go.kr/data/15058747/openapi.do' },
      ],
    };
  },

  methods: {
    // url2
    callApi(apiUrl) {
      // console.log('apiUrl :: ',apiUrl);
      axios
        .get(apiUrl)
        .then((response) => {
          console.log(response.data);
          this.arr = response.data.response.body.items.item;
          this.arrKey = Object.keys(this.arr[0]);
          // console.log(this.arrKey);
        })
        .catch((error) => {
          console.log(location.origin);
          console.log(error);
        });
    },
  },
