          str += '"' + key + '" : "' + value + '"';
        } else {
          str += '"' + key + '" : ' + value;
        }
        // console.log('idx : ', idx, ' / cnt : ', cnt);
        if ( idx < cnt-1 ){
          str += ', ';
        }
        idx = idx+1;
