<template>
    Dashboard > 나의 반려식물 현황 Dashboard 입니다.<br>
    <!-- {{ this.myPlantArr }} -->
    <div v-for="item in this.myPlantResult" v-bind:key="item.crp_id">
        당신의 {{item.crp_nm}} ... 물을 마신지 <b>{{item.days_since_water}}</b>일 지났습니다.
    </div>
</template>

<script>
var itemId;
var itemName;
var daysSinceWater;
var myPlantResult;

  export default {
    name: 'MainComponent',
    components: {
      // Menu1_Nav: Menu1_Nav,
    },
    data() {
        return {
            itemId
            , itemName
            , daysSinceWater
            , myPlantResult
            }
        },
      created() {
            this.initialize ();
      },
      beforeMount() {

      },
      mounted() {
      },
      methods: {
            initialize () {
              const axios = require("axios");
              axios
                .post("/api/crop_cultivate/crop_cultivate_user", { contentsId: 15, usrId: 1})
                // axios.post(this.router, resultJsonImp)
                .then((response) => {
                    var myPlantJson = response.data;
                    var myPlantArr = myPlantJson[0].get_result_json;
                    // var myPlantKey = Object.keys(myPlantArr[0]);

                    this.myPlantResult = myPlantArr;
                })
                .catch((error) => {
                  console.log(error);
                });
              
            },
        },
    }
</script>