<template>
  <h6>[Menu5-2.vue]</h6>
  <iframe width="560" height="315" src="https://www.youtube.com/embed/XiPYHe5qo0A" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
  <!-- 조회form -->
  <div class="container-column" v-if="viewMode">
    <input type="button" class="button-board" value="목록으로" v-on:click="this.viewMode=false; this.listMode=true;" />
    <input type="text" class="board-title" v-model="this.title" />
    <textarea class="board-contents" cols="30" rows="20" v-model="this.contents" />
  </div>
  <div class="row">
    <table>
      <thead>
        <tr>
          <th v-for="(a, i) in resultKey" :key="i">{{ this.resultKey[i] }}</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(row, rid) in resultArr"
          :key="rid"
          v-on:click="viewData(row.heal_act_id)"
          style="cursor: pointer"
        >
          <td v-for="(col, cid) in row" :key="cid" v-text="col"></td>
        </tr>
      </tbody>
    </table>
  </div>
  <div class="row">...페이지...</div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      listMode: true,
      viewMode: false,
      // writeMode: false,
      // editMode: false,
      title: "",
      contents: "",
      resultArr: [],
      resultKey: [],
      img: "@/assets/images/plant1_01.jpg",
    };
  },
  mounted() {
    this.contentsList();
    // console.log(this.img);
  },
  methods: {
    contentsList() {
      this.listMode = true;
      this.viewMode = false;
      // this.writeMode = false;
      // this.editMode = false;
      
      const axios = require("axios");
      axios
        .post("/api/healActBoard", { dml: '', contentsId: 13, tag: '친환경' })
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          this.resultJson = response.data;
          this.resultArr = this.resultJson[0].get_result_json;
          this.resultKey = Object.keys(this.resultArr[0]);
          // delete this.resultKey[2];
      this.inert = false;
      this.update = false;
        })
        .catch((error) => {
          console.log(error);
        });
      this.healActId = null;
      this.inert = false;
      this.update = false;
    },
    viewData(healActId) {
      this.listMode = false;
      this.viewMode = true;
      // this.writeMode = false;
      // this.editMode = true;

      var resultJson;
      var resultArr;
      var resultKey;

      const axios = require("axios");
      axios
        .post("/api/healActBoard", { dml: '', healActId: healActId})
        .then((response) => {
          resultJson = response.data;
          resultArr = resultJson[0].get_result_json;
          resultKey = Object.keys(resultArr[0]);

          // console.log('this.resultArr = ',this.resultArr);
          this.title = resultArr[0].title;
          this.contents = resultArr[0].contents;
          this.healActId = healActId;
          // console.log('this.title = ', this.title);
        })
        .catch((error) => {
          console.log(error);
        });
        this.listMode=false;
        // if(수정권한있으면(작성자,관리자)) 
        //   this.editMode=true;
        // else 
        //   this.viewMode=true;
        this.editMode=true;
    },
    
  },
};
</script>

<style>
/* @import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap"); */
</style>
