<template>
  <div class="contents_div_1" style="height:95%; overflow:auto;">
    <div style="padding:1rem; margin-right:1rem; margin-bottom:1rem; border=1px solid red;">
      <h2>TO-DO LIST 검색 기능 개발 중</h2>
      1. 입력(날짜/제목/입력)<br />
      2. 목록(번호/제목/날짜)<br />
      3. 페이징 <br />
      4. 글자 수 조정하기(textarea) : https://ui.toast.com/posts/ko_20220318
      <h5>
        listMode : {{this.listMode}} / viewMode : {{this.viewMode}} / writeMode : {{this.writeMode}} / editMode : {{this.editMode}}</h5>
      <div class="container-column" v-if="listMode">
        <input type="button" class="button-board" value="새글쓰기" v-on:click="this.writeMode=true; this.title=null; this.contents=null;" v-if="!this.writeMode" />
        <div style="border: 1px solid blue;">
          <button class="button-card" v-for="item in this.tagList" v-bind:key="item.id" v-on:click="this.contentsList(item.id, item.tagValue);">{{ item.tagName }}</button>
        </div>
      </div>
      <!-- 조회form -->
      <div class="container-column" v-if="viewMode">
        <input type="button" class="button-board" value="목록으로" v-on:click="this.viewMode=false; this.listMode=true;" />
        <input type="text" class="input-text" v-model="this.title" />
        <textarea class="textarea-basic" cols="30" rows="20" v-model="this.contents" />
      <input type="button" class="button-board" value="수정(권한에 따라서 버튼 제거 가능)" v-on:click="doUpdate();" />
      </div>

      <!-- 새글form -->
      <div class="container-column" v-if="writeMode">
        <input type="button" class="button-board" value="목록으로" v-on:click="this.writeMode=false; this.listMode=true;" />
        <input type="text" class="input-text" v-model="this.title" placeholder="제목을 입력해주세요." />
        <textarea class="textarea-basic" cols="30" rows="20" v-model="this.contents" style="resize: none" />
        <input type="button" class="button-board" value="등록하기" v-on:click="doDml('insert');" />
      </div>

      <!-- 수정form -->
      <div class="container-column" v-if="editMode">
      <input type="button" class="button-board" value="목록으로" v-on:click="this.editMode=false; this.listMode=true;" />
        <input type="text" class="input-text" v-model="this.title" />
        <textarea class="textarea-basic" cols="30" rows="20" v-model="this.contents" />
        <input type="button" class="button-board" value="수정" v-on:click="doDml('update');" />
        <input type="button" class="button-board" value="삭제" v-on:click="doDml('delete');" />
      </div>

      <div class="row">
        <table>
          <thead>
            <tr>
              <th v-for="(a, i) in resultKey" :key="i">{{ this.resultKey[i] }}</th>
            </tr>
          </thead>
          <tbody>
            <!-- <tr v-for="(row,rid) in resultArr" :key="rid" v-on:click="viewData({{row.heal_act_id}});" style="cursor:pointer;"> -->
            <tr
              v-for="(row, rid) in resultArr"
              :key="rid"
              v-on:click="viewData(row.heal_act_id)"
              style="cursor: pointer"
            >
              <td v-for="(col, cid) in row" :key="cid" v-text="col"></td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="row">...페이지...</div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      inputTag: "",
      tagId: "0",
      tagList: [
        { id: 0, tagName: "전체", tagValue: undefined},
        { id: 1, tagName: "친환경", tagValue: "친환경"},
        { id: 2, tagName: "재활용재사용", tagValue: "재활용재사용"},
        { id: 3, tagName: "비용절감", tagValue: "비용절감"},
        { id: 4, tagName: "반려식물", tagValue: "반려식물"},
        { id: 5, tagName: "흙", tagValue: "흙"},
        { id: 6, tagName: "빛", tagValue: "빛"},
        { id: 7, tagName: "온", tagValue: "온"},
      ],
      listMode: true,
      viewMode: false,
      writeMode: false,
      editMode: false,
      title: "",
      contents: "",
      resultArr: [],
      resultKey: [],
      img: "@/assets/images/plant1_01.jpg",
    };
  },
  created() {
  },
  mounted() {
    this.contentsList();
    // console.log(this.img);
  },
  methods: {
    contentsList(id, tagName) {
      var contentsId = null;
      if (id == 0 || id == undefined) {
        contentsId = 9;
      } else {
        contentsId = 13;
      }
      // console.log(contentsId, ' ', tagName);

      this.listMode = true;
      this.viewMode = false;
      this.writeMode = false;
      this.editMode = false;
      
      const axios = require("axios");
      axios
        // .post("/api/healActBoard", { dml: '', contentsId: "9" })
        .post("/api/healActBoard", { dml: '', contentsId: contentsId, tag: tagName })
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          this.resultJson = response.data;
          this.resultArr = this.resultJson[0].get_result_json;
          this.resultKey = Object.keys(this.resultArr[0]);
          // delete this.resultKey[2];
      this.inert = false;
      this.update = false;
        })
        .catch((error) => {
          console.log(error);
        });
      this.healActId = null;
      this.inert = false;
      this.update = false;
    },
    viewData(healActId) {
      this.listMode = false;
      this.viewMode = false;
      this.writeMode = false;
      this.editMode = true;

      var resultJson;
      var resultArr;
      var resultKey;

      const axios = require("axios");
      axios
        .post("/api/healActBoard", { dml: '', healActId: healActId })
        .then((response) => {
          resultJson = response.data;
          resultArr = resultJson[0].get_result_json;
          resultKey = Object.keys(resultArr[0]);
          
          this.title = resultArr[0].title;
          this.contents = resultArr[0].contents;
          this.healActId = healActId;
          // console.log('this.title = ', this.title);
        })
        .catch((error) => {
          console.log(error);
        });
        this.listMode=false;
        // if(수정권한있으면(작성자,관리자)) 
        //   this.editMode=true;
        // else 
        //   this.viewMode=true;
        this.editMode=true;
    },
    doDml(dml) {
      if ( this.title.length > 0 && this.contents.length > 0) {
        
        var resultMsg;
        const axios = require("axios");
        axios
          .post("/api/healActBoard", { dml: dml, healActId: this.healActId, title: this.title, contents: this.contents})
          .then((response) => {
            // console.log('response.data : ',response.data.result);
            resultMsg = response.data.result;
            console.log('resultMsg : ',resultMsg);
              
            // alert(resultMsg);
            this.editMode = false;
            this.listMode = true;
            this.contentsList();
          })
          .catch((error) => {
            console.log(error);
          });   
      } else {
        alert("제목과 내용 입력은 필수입니다.");
      }
    },
    
  },
};
</script>

<style>
/* @import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap"); */
</style>
