<template>
  <div class="contents_div_1">
    <div class="contents_div_2">
      <div class="chart_line">
        <table>
          <thead>
            <tr>
              <th v-for="(a, i) in resultKey" :key="i">{{ this.resultKey[i] }}</th>
            </tr>
          </thead>
          <tbody>
            <!-- <tr v-for="(row,rid) in resultArr" :key="rid" v-on:click="viewData({{row.heal_act_id}});" style="cursor:pointer;"> -->
            <tr v-for="(row, rid) in resultArr" :key="rid" style="cursor: pointer">
              <td v-for="(col, cid) in row" :key="cid" v-text="col"></td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="contents_div_2">
      <div class="chart_line">
        <!-- <Line v-if="loaded" :chart-options="chartOptions" :chart-data="chartData" /> -->
        <LineChart v-if="loaded"
                    :chartData="chartData"
                    :chartOptions="chartOptions"
          />
      </div>
    </div>
  </div>
</template>


<script>
import LineChart from '../Charts/LineChart.vue'

const axios = require("axios");

var moment = require("moment"); // moment 모듈불러오기
var vStartCreateDt = moment().subtract(12, "months"); //
var vEndCreateDt = moment().subtract(0, "months");
var vStartYMD = String(vStartCreateDt.format("YYYYMMDD"));
var vEndYMD = String(vEndCreateDt.format("YYYYMMDD"));
var vStartYM = String(vStartCreateDt.format("YYYYMM"));
var vEndYM = String(vEndCreateDt.format("YYYYMM"));

var serviceKey = 'Hh%2BfYpk8K0x9lCg%2FzZt3%2B5FU8g5T2yNfBDwrrQ%2FnB99%2BJXNxS03XShrSbnwyHCpiweqMs0i50MmGDKfUOTKJfQ%3D%3D'
// 공공데이터활용지원센터_보건복지부 코로나19 감염 현황
// var url1 = "http://openapi.data.go.kr/openapi/service/rest/Covid19/getCovid19InfStateJson"
var url1 = "/openapi/service/rest/Covid19/getCovid19InfStateJson";
url1 += "?" + encodeURIComponent("serviceKey") + "=" + serviceKey; /* Service Key*/
url1 += "&" + encodeURIComponent("pageNo") + "=" + encodeURIComponent("1"); /* */
url1 += "&" + encodeURIComponent("numOfRows") + "=" + encodeURIComponent("10"); /* */
url1 += "&" + encodeURIComponent("startCreateDt") + "=" + encodeURIComponent(vStartYMD); /* */
url1 += "&" + encodeURIComponent("endCreateDt") + "=" + encodeURIComponent(vEndYMD); /* */

var xmlDoc;
var test;
var resultArr; 
var resultKey;

var chartData;
var chartOptions;

export default {
  components: {
    LineChart
  },
  data() {
    return {     
      xmlDoc,
      test,
      resultArr, 
      resultKey,
            loaded: false,
            chartData, 
            chartOptions, 
    };
  },
  created() {
    this.loaded = false;
    var labels = [];
    var tmpJson;
    var deathCnt = [];
    var decideCnt = [];
    var datasets = [];

    axios
      .get(url1)
      .then((response) => {
        // this.xmlDoc = response.data.response.body.items.item;
        this.resultJson = response.data.response.body.items.item;
        // console.log('xmlDoc : ', xmlDoc);
        this.resultArr = response.data.response.body.items.item;
        this.resultKey = Object.keys(this.resultArr[0]);
        // console.log('resultKey : ', this.resultKey);
        // console.log('resultArr : ', this.resultArr);
        var createDtTmp;
        for (let i in this.resultArr) {
          tmpJson = this.resultArr[i];
          // console.log('tmpJson : ', tmpJson.createDt);
          // createDtTmp = Date(tmpJson.createDt);
          createDtTmp = tmpJson.createDt;
          // labels[i] = createDtTmp.substring(0, 4)+'_'+createDtTmp.substring(5, 7);
          labels[i] = createDtTmp.substring(0, 4)+'_'+createDtTmp.substring(5, 7)+'_'+createDtTmp.substring(8, 11);
          deathCnt[i] = tmpJson.deathCnt;
          decideCnt[i] = tmpJson.decideCnt;
          // console.log(createDtTmp, ' : ', labels[i]);
        }
        
        // "datasets" : [{
        //               "label" : "강남구", 
        //               "backgroundColor" : "#058d01", 
        //               "data" : [592,864,949]}]}

        
        datasets[0] = { "label": "decideCnt"
                      , "backgroundColor" : "#FFBE22"
                      , "data": decideCnt.reverse()
                    };
        // datasets[1] = {"label": "deathCnt", "backgroundColor" : "#058d01", "data": deathCnt.reverse()};
        // console.log(datasets);
        // this.chartData = { labels: resultJson.lables, datasets: resultJson.datasets };
        this.chartData = { labels: labels.reverse(), datasets: datasets };
        this.chartOptions = { responsive: true, maintainAspectRatio: false , reversed: true};
        // console.log("this.chartData :: ", this.chartData);
        this.loaded = true;
        // console.log(this.loaded);
      })
      .catch((error) => {
        console.log(error);
      });
  },
  mounted() {
    
  },
  methods: {

  },
};
</script>
<style></style>
