<template>
  <div class="contents_div_1_title">
    <h3>** 최종 재배 활동 **</h3>
  </div>
  <div class="contents_div_1">
        <!-- {{ this.myRecordsArr }} -->
        <div style="width:90%; margin-left:2rem;">
          <table class=".table-common" >
            <tr>
              <th class=".th-common" width="15%">작물명</th>
              <th class=".th-common" width="15%">반려식물이름</th>
              <th class=".th-common"  width="15%" @click="sortList('cltvt_act_cd_100')">물주기&#8597;</th>
              <th class=".th-common"  width="15%" @click="sortList('cltvt_act_cd_200')">비료&#8597;</th>
              <th class=".th-common"  width="15%" @click="sortList('cltvt_act_cd_300')">해충박멸&#8597;</th>
              <th class=".th-common"  width="15%" @click="sortList('cltvt_act_cd_400')">분갈이&#8597;</th>
            </tr>
            <tr v-if="this.myRecordsArr" v-for="(a, i) in this.myRecordsArr" :key="i">
              <td>{{ this.myRecordsArr[i].crp_nm }}</td>
              <td>{{ this.myRecordsArr[i].cmplnt_nm }}</td>
              <td style="border:2px solid orange;">{{ this.myRecordsArr[i].cltvt_act_cd_100 }} - {{ this.myRecordsArr[i].cltvt_act_cd_100_dt }}</td>
              <td>{{ this.myRecordsArr[i].cltvt_act_cd_200 }} - {{ this.myRecordsArr[i].cltvt_act_cd_200_dt }}</td>
              <td>{{ this.myRecordsArr[i].cltvt_act_cd_300 }} - {{ this.myRecordsArr[i].cltvt_act_cd_300_dt }}</td>
              <td>{{ this.myRecordsArr[i].cltvt_act_cd_400 }} - {{ this.myRecordsArr[i].cltvt_act_cd_400_dt }}</td>
            </tr>
            <tr v-else>
              <td colspan="6">재배 활동 기록이 없습니다.</td>
            </tr>
          </table>
        </div>
    </div>
  <div class="contents_div_1_title">
    <h3>** to do list (일/주/월) **</h3>
  </div>
  <div class="contents_div_1">
    <div class="contents_div_2">ㅇㅇㅇ
    </div>
    <div class="contents_div_2">ㅁㅁㅁ
    </div>
  </div>
  <div class="contents_div_1_title">
    <h3>** 준비물 **</h3>
  </div>
  <div class="contents_div_1">
    <div class="contents_div_2">ㅇㅇㅇ
    </div>
    <div class="contents_div_2">ㅁㅁㅁ
    </div>
  </div>
</template>

<script>
const axios = require("axios");
var resultJson;

export default {
  components: {
  },
  data() {
    return {
      usrId : null,
      myRecordsArr: null,
      actvt1:null,
      actvt2:null,
      actvt3:null,
      splitTest:null,

      sortedData: [],
      sortedbyASC: true,
    };
  },
  created() {
    this.usrId = 1; /////////////////
    this.getCltvtSummary(this.usrId);
  },
  mounted() {
  },
  methods: { 
    getCltvtSummary(usrId) {
      axios
        .post("/api/plant", { contentsId: 23, usrId: usrId })
        .then((response) => {
          var myPlantJson;

          myPlantJson = response.data;
          this.myRecordsArr = myPlantJson[0].get_result_json;

          // for (var i in this.myPlantArr) {
          //   this.crp_nm = this.myPlantArr[i].crp_nm;
          //   this.cmplntNm = this.myPlantArr[i].cmplnt_nm;
          //   this.actvt2 = this.myPlantArr[i].비료;
          //   this.actvt3 = this.myPlantArr[i].해충박멸;
          // }
          this.sortedData = this.myRecordsArr;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    sortList(sortBy) {
      if (this.sortedbyASC) {
        this.sortedData.sort((x, y) => (x[sortBy] > y[sortBy] ? -1 : 1));
        this.sortedbyASC = false;
      } else {
        this.sortedData.sort((x, y) => (x[sortBy] < y[sortBy] ? -1 : 1));
        this.sortedbyASC = true;
      }
    },
  },
};
</script>

