<template>
    <Bar
        id="my-chart-id"
        :data="chartData"
        :options="chartOptions"
    />
    <!-- <Bar
        id="my-chart-id"
        :data="chartData"
    /> -->
</template>

<script>
import { Bar } from 'vue-chartjs'
import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js'

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale)

var labelArray = ["James", "Mark", "Simon"],
  greenData = [55, 82, 32],
  orangeData = [27, 10, 53],
  greyData = [18, 8, 15];

export default {
    name: 'BarChart',
    components: { Bar },
    props : {
        chartData: Object,
        chartOptions: Object,
    },
    data() {
        return {
            
            // chartData: {
            //     labels: [ 'January', 'February', 'March' ],
            //     datasets: [ { data: [40, 20, 12] } ]
            // },
            // chartData: {"labels" : ["로메인1-채종", "내 이름", "내 이름"], "datasets" : [{"data" : [41,20,19]}]},
            // chartData: {"labels" : ["로메인1-채종", "내 이름", "내 이름"], "datasets" : [{"data" : [41,40,19]}]},

            // chartOptions: {
            //     responsive: true
            // }
            chartData: {
                labels: labelArray,
                datasets: [{
                    label: 'Green %',
                    data: greenData,
                    backgroundColor: 'rgb(0,166,149)',
                    borderColor: 'rgb(0,166,149)',
                    borderWidth: 1
                },
                {
                    label: 'Orange %',
                    data: orangeData,
                    backgroundColor: 'rgb(229,117,31)',
                    borderColor: 'rgb(229,117,31)',
                    borderWidth: 1,
                },
                {
                    label: 'Grey %',
                    data: greyData,
                    backgroundColor: 'rgb(179,179,179)',
                    borderColor: 'rgb(179,179,179)',
                    borderWidth: 1,
                }
                ]
            },
            chartOptions: {
                scales: {
                    xAxes: [{
                        stacked: true,
                    }],
                    yAxes: [{
                        stacked: true
                    }]
                },
                plugins: {
                    datalabels: {
                        color: 'white',
                        font: {
                        weight: 'bold'
                        },
                        formatter: function(value, context) {
                        return Math.round(value) + '%';
                        }
                    }
                }

            }
            
            
        }
    }
}
</script>