<template>
  <div class="contents_div_1">
    <!-- <div style="padding:1rem; margin-right:1rem; margin-bottom:1rem; border=1px solid red;"> -->
      <div>
        <button v-for="item in selectList" v-bind:key="item.id" v-on:click="contentsList(item.contentsId);">{{item.names}}</button>
      </div>
      <!-- 검색 기능 -->
      <div class="container-row">
          <!-- Search Componenet -->
          <div class="boxes-small" v-for="item in searchList" v-bind:key="item.id">
              <input type="radio" name="stdSearch" id="{{item.id}}" value="{{item.router}}" v-on:change="setData(item.contentsId, item.router);">
              <label for="{{item.id}}">{{item.names}}</label>
          </div>
      </div>
      <div class="container-row">
          <input type="text" placeholder="검색어를 입력해주세요" v-model="pInput" /><button v-on:click="searchData(pInput)">검색</button>
      </div>
    <!-- </div> -->
  </div>
  <div class="contents_div_1" style="height:80%; overflow-y: scroll; margin-top:0;">
    <div style="padding:1rem; margin-right:1rem; margin-bottom:1rem; border=1px solid red;">
      <!-- 조회 결과 -->
      <div class="row">
        <table class=".table-sm">
        <thead> 
            <tr>
            <th v-for="(a,i) in resultKey" :key="i">{{ this.resultKey[i] }}</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(row,rid) in resultArr" :key="rid">
            <td v-for="(col,cid) in row" :key="cid" v-text="col"></td>
            </tr>
        </tbody>
        </table>
      </div>
    </div>
  </div>
    
</template>

<script>
  import Menu6_Nav from './Menu6_Nav';
  var resultJson;
  // import resultJson from './resultVal.json'
  var contents_id;
  var resultJson;
  var pInput;
  var resultArr; 
  var contentsId;
  var searchWord;
  var router;

  export default {
    components: {
              Menu6_Nav: Menu6_Nav
          },
    data() {
          return {
            selectList: [
                {names: '표준단어', router: '/api/da_std', id: 1, contentsId: 5},
                {names: '표준용어', router: '/api/da_std', id: 2, contentsId: 6},
              ],
            setData(contentsId, router){
                // alert('contentsId, router::'+contentsId, +'::'+ router);
                this.contentsId = contentsId;
                this.router = router;
              },
            searchList: [
                  {names: '표준단어검색', router: '/api/da_std_word_search', id: 1, contentsId: 7},
                  {names: '표준용어검색', router: '/api/da_std_word_search', id: 2, contentsId: 8},
              ],
            resultJson,
            resultArr: [],
            resultKey: [],

            pInput,
            resData: [],
            contentsId,
            searchWord,
        }
      },
    mounted() {
        this.contentsList(5);
    },
    methods: {
      contentsList(contentsId){
        const axios = require('axios');
        axios.post('/api/da_std', {contentsId: contentsId})
        // axios.post(this.router, resultJsonImp)
                .then(response => {
                    this.resultJson = response.data;
                    // console.log('Test.vue::',response.data);
                    this.resultArr = this.resultJson[0].get_result_json;
                    this.resultKey = Object.keys(this.resultArr[0]);
                })
                .catch(error => {
                    console.log(error)
                })


                
        // const axios = require('axios');
        // this.$axios.post('/da_std_word')
        //     .then(response => {
        //             this.resultJson = response.data;
        //             this.resultArr = this.resultJson[0].get_result_json;
        //             this.resultKey = Object.keys(this.resultArr[0]);
        //     })
        //     .catch(error => {
        //         console.log(error)
        //     })
      },
      searchData(pInput){
          if(pInput == '') {
              alert('키워드가 없습니다!');
          } else {
              // alert(this.router+'::'+this.contentsId +'::' + pInput);
              const axios = require('axios');
              axios.post(this.router, {contentsId: this.contentsId, searchWord: pInput})
              // axios.post(this.router, resultJsonImp)
                      .then(response => {
                          console.log('Test.vue::',response.data);
                          this.resultJson = response.data;
                          // console.log('Test.vue::',response.data);
                          this.resultArr = this.resultJson[0].get_result_json;
                          this.resultKey = Object.keys(this.resultArr[0]);
                      })
                      .catch(error => {
                          console.log(error)
                      })
          }
      },
    },  
  }
</script>

