<script>
  import Menu4_Nav from './Menu4_Nav';
  import resultJson from './resultVal.json'
  import resultJson_def from './resultVal_def.json'
  import resultData from './resultData.json'

  var contents_id;
  var resultCol;
  // var resultArr;

  export default {
    components: {
              Menu4_Nav: Menu4_Nav
          },
    data() {
