<template>
  <div class="contents_div_1_title">
    <h3>** 나의 반려식물 통계</h3>
  </div>
  <div class="contents_div_1">
    <div class="contents_div_2">
      <h3>재배 활동</h3>
      <div class="chart_line">
        <!-- <BarChart v-if="loaded" /> -->
        <BarChart v-if="loaded" :chartData="chartData" :chartOptions="chartOptions" :height="300"
                  ref="myBarChart"/>
      </div>
    </div>
    <div class="contents_div_2">
      <div v-if="showDetail">
        <h3>cltvtId:{{ this.cltvtId }} cmplntNm:{{ this.cmplntNm }} recordCnt:{{ this.recordCnt }} </h3>
        <h3>최근 1개월</h3>
        <div class="contents_div_1">
          <BarChart :chartData="chartData2" :chartOptions="chartOptions2" :height="300"/>
        </div>
        
      </div>
      <div v-else>
        그래프 항목을 클릭하세요.
      </div>
    </div>
  </div>
  <div class="contents_div_1_title">
    <h3>** ㅇㅇㅇㅇ</h3>
  </div>
  <div class="contents_div_1">
    <div class="contents_div_2">ㅇㅇㅇ
    </div>
    <div class="contents_div_2">ㅁㅁㅁ
    </div>
  </div>
</template>

<script>
import BarChart from '../Charts/BarChart.vue';
const axios = require("axios");
var resultJson;

export default {
  components: {
    BarChart
  },
  data() {
    return { 
      loaded: true,
      chartData: {
            labels: [],
            datasets: [ { data: [] } ]
        },
      chartData2: {
            labels: [],
            datasets: [ { data: [] } ]
        },
      recordCnt: null,
      cltvtId: null,
      cmplntNm: null, 
      cmplntNm: null, 
      showDetail: false,

      myRecordsArr: null,
    };
  },
  created() {
    // barchart
    axios
      .post("/api/lineChartDb", { contentsId: 20, userId: 1 })
      .then((response) => {
        resultJson = response.data[0].get_result_json;
        // console.log('resultJson ::: ',resultJson);

        this.chartData = { 
          type: 'bar',

          labels: resultJson.labels,
          datasets: resultJson.datasets,
        };

        // this.chartOptions = { responsive: true, maintainAspectRatio: false };
        this.chartOptions = {
          onClick: this.handleChartClick,

          responsive: false,  // 크기를 변경해주려면 false
          // maxBarThickness: 50,

          maintainAspectRatio: false,

          // Elements options apply to all of the options unless overridden in a dataset
          // In this case, we are setting the border of each horizontal bar to be 2px wide
          elements: {
            bar: {
              borderWidth: 1,
            }
          },

          plugins: {
            // title
            title: {
              display: true,
              text: 'Chart.js Horizontal Bar Chart'
            },
            legend: {
              position: 'bottom',
              labels: {
                // fontColor: "red",
                // fontSize: 18,
                // This more specific font property overrides the global property
                font: {
                        size: 14,
                    }
              },
              fullSize: true,
            },
          },
          scales: {
            y: {
              beginAtZero: true,
              
              ticks: {
                autoSkip: false,
                labelOffset: 4,
                padding: 4,
                font: {
                  // size: 16,
                },
                color: "red",
              },

              grid: {
                display: false, //뒷배경 라인 없애기
              },

            }
          },
          
          //horizontal bar chart
          indexAxis: 'y',

          
        }

        this.loaded = true;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  mounted() {
  },
  methods: { 
    handleChartClick(evt, elements) {
      // // var chart = this.$refs.myBarChart.$data._chart; xx
      // // var chart = this.$refs.myBarChart.chartData;
      // var chartIndex = this.$refs.myBarChart;
      // console.log('chartIndex : ', chartIndex);

      // console.log('<-------------');
      // // console.log(this.$refs.myBarChart.$attrs);
      // console.log(this.$refs.myBarChart.$data);
      // console.log(evt[0]);
      // console.log('------------->');

      // console.log(this.$refs.myBarChart.chartData.datasets);
      var chartDataTmp = this.$refs.myBarChart.chartData.datasets;
      var chartData = chartDataTmp[0];
      
      // console.log('chartData :: ', chartData);
      // console.log('data : ', test[0].data[0], '//cltvt_id : ', test[0].cltvt_id[0]);
      // console.log('data : ', test[0].data[1], '//cltvt_id : ', test[0].cltvt_id[1]);
      // console.log('data : ', test[0].data[2], '//cltvt_id : ', test[0].cltvt_id[2]);
      // console.log(this.$refs.myBarChart.chartData.datasets.data);
      // console.log(this.$refs.myBarChart.chartData.datasets.cltvt_id);

      var clickIdx = elements[0].index;
      
      this.recordCnt = chartData.data[clickIdx];
      this.cltvtId = chartData.cltvt_id[clickIdx];
      this.cmplntNm = chartData.cmplnt_nm[clickIdx];
      this.crpNm = chartData.crp_nm[clickIdx];

      // this.getUserRecords(1, this.cltvtId );
      this.getCltvtActDet(this.cltvtId);
      this.showDetail = true;
    },
    getCltvtActDet(cltvtId) {
      axios
      .post("/api/lineChartDb", { contentsId: 21, cltvtId: cltvtId })
      .then((response) => {
        resultJson = response.data[0].get_result_json;
        // console.log('resultJson ::: ',resultJson);

        this.chartData2 = { 
          type: 'bar',

          labels: resultJson.labels,
          datasets: resultJson.datasets,
        };

        // this.chartOptions = { responsive: true, maintainAspectRatio: false };
        this.chartOptions2 = {
          // onClick: this.handleChartClick,

          responsive: false,  // 크기를 변경해주려면 false
          // maxBarThickness: 50,

          maintainAspectRatio: false,

          // Elements options apply to all of the options unless overridden in a dataset
          // In this case, we are setting the border of each horizontal bar to be 2px wide
          elements: {
            bar: {
              borderWidth: 1,
            }
          },

          plugins: {
            // title
            title: {
              display: true,
              text: 'Chart.js Horizontal Bar Chart'
            },
            legend: {
              position: 'bottom',
              labels: {
                // fontColor: "red",
                // fontSize: 18,
                // This more specific font property overrides the global property
                font: {
                        size: 14,
                    }
              },
              fullSize: true,
            },
          },
          scales: {
            y: {
              beginAtZero: true,
              
              ticks: {
                autoSkip: false,
                labelOffset: 4,
                padding: 4,
                font: {
                  // size: 16,
                },
                color: "red",
              },

              grid: {
                display: false, //뒷배경 라인 없애기
              },

            }
          },
          
          //horizontal bar chart
          indexAxis: 'y',
        }

        this.loaded = true;
      })
      .catch((error) => {
        console.log(error);
      });
    },
    getUserRecords(usrId, cltvtId) {
      var myPlantRecordJson;
      var recrdDttm; 
      var cltvtActCd;
      var cntnts;
      this.myRecordsArr = null;
      console.log('usrId : ', usrId, ' cltvtId : ', cltvtId);

      axios
        .post("/api/plant", { contentsId: 16, usrId: usrId, cltvtId: cltvtId })
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          myPlantRecordJson = response.data;
          this.myPlantRecordArr = myPlantRecordJson[0].get_result_json;
          var myRecordsJsonTmp = {};
          var myRecordsJsonArrayTmp = [];
          for (var i in this.myPlantRecordArr) {
            if (this.myPlantRecordArr[i].cltvt_id == cltvtId) {
              recrdDttm = this.myPlantRecordArr[i].recrd_dttm;
              cltvtActCd = this.myPlantRecordArr[i].cltvt_act_cd;
              cntnts = this.myPlantRecordArr[i].cntnts;

              var myRecordsJsonTmp = {
                recrdDttm: recrdDttm,
                cltvtActCd: cltvtActCd,
                cntnts: cntnts,
              };
              myRecordsJsonArrayTmp.push(myRecordsJsonTmp);
              this.myRecordsArr = myRecordsJsonArrayTmp;
            }
          }
          // console.log('this.myRecords length :::: ', this.myRecords.length);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getMyPlantChartDetail(){

    }
  },
};
</script>

<style>
