  <div class="contents_div_1_title">
    <h3 id="section1" style="margin-top:-6rem; padding-top:6rem;">1. 아이디어 사업화의 개념</h3>
  </div>
  <div class="contents_div_1">
    <div class="pre">
      <pre>

​

아이디어를 가지고 제품을 제조하여 판매하는 것을 모든 절차를 '아이디어 사업화'라고 볼 수 있습니다.

먼저 귀하의 아이디어가 상업적으로 성공가능한 것인지에 대해 진지하게 고민하셔야 합니다. 소비자는 자신의 욕구를 만족시켜 줄 제품을 구입하는 것입니다. 그러나 저희 특허 시제품 제작 과정에서 보면 특허사업화를 진행하는 분들중에 일부는 본인의 아이디어가 기존에 세상에 없는 새로운 아이디어라는 점만을 강조하지만, 해당 아이디어의 상업적 성공가능성에 대한 검토가 부족한 경우가 많습니다. 즉, 소비자의 욕구 파악(시장조사)하는 것을 가볍게 생각하시는 경향이 있습니다. 뿐만 아니라 시장 조사 이외에 아이디어를 실제 시제품으로 제작하여 상업적 성공가능성에 지대한 영향을 미치는 실제 소비자 반응 테스트, 제조 원가 산정 등도 검토하셔야 합니다(후술).

​  ​    </pre>
    ​</div>
  </div>
  <div class="contents_div_1_title">
    <h3 id="section2" style="margin-top:-6rem; padding-top:6rem;">2. 시제품 제작에 의한 상업적 성공가능성의 검토</h3>
  </div>
  <div class="contents_div_1">
    <div class="pre">
      <pre>이제 아이디어의 상업적 성공가능성에 대해 살펴봅니다. 귀하의 아이디어를 실제 제품으로 만들었을 때의 느낌은 완전히 다릅니다. 따라서 아이디어의 상업적 성공가능성을 판단하기 위해서는 아이디어를 실제 제품으로 제작해서 해당 아이디어 제품이 상업적 성공가능성이 있는지 테스트를 해보셔야 합니다. 시제품을 제작하여 해당 아이디어가 기능이 잘 구현되는지(실제 기능 구현이 어려운 경우도 많음), 제조원가가 적합한지(너무 비싸면 판매가능성이 떨어짐), 소비자의 반응이 좋은지(직접 사용하게 한 후의 반응 점검) 등 다양한 조사를 통해 특허 아이디어의 상업적 성공가능성을 검토하게됩니다. 저희 특허 시제품 제

작 경험에 따르면 특허 시제품 의뢰자 일부는 시제품을 통해 상업적 성공가능성에 대한 확신을 얻지만 일부는 상업적 성공가능성이 없다고 판단하여 바로 특허사업화를 포기하기도 합니다.

​

참고로 시제품 제작을 위해서는 아이디어를 구체화해야 합니다. 즉, 시제품을 제작하려면 제품의 정확한 수치, 재질, 기능이 정의되어야 합니다. 이와 같이 정확하게 정의된 제품의 구조를 설계하는데 이를 '기구설계'라고 하며,  기구설계 전문가에 의해 진행되는 것이 일반적입니다. 이와 같은 설계를 바탕으로 시제품 실물 제작에 들어갑니다. 시제품을 제작할 때 시제품의 완성정도에 따라서 전체 제품의 일부만을 제작하여 주요 기능만을 테스트하는 수준으로 할 것인지 아니면 실제 판매를 고려한 실제품 수준으로 할 것인지 등 시제품 완성도를 다양하게 선택하실 수 있습니다(제작 비용을 고려하여 선택). 주의할 점은 시제품 제작 전과정에서 의뢰자와 제작자간의 정호가한 커뮤니케이션이 매우 중요하며, 이를 위해 가급적 모든 의사소통은 문서화하여 진행하는 것이 바람직합니다.  ​    </pre>
    ​</div>
