  <div class="contents_div_1_title">
    <h3 id="section3" style="margin-top:-6rem; padding-top:6rem;">3. 본격적인 아이디어 사업화</h3>
  </div>
  <div class="contents_div_1">
    <div class="pre">
      <pre>
다음 단계로 시제품 제작 후 특허 아이디어의 상업적 성공가능성이 '매우 클 때' 비로서 본격적인 사업을 검토하게 됩니다(참고로 소비자에게 꼭 필요한 제품이어야 하며 '있으면 좋은' 정도의 제품은 성공가능성이 낮은 것임). 아이디어 사업화도 일반적인 제품 개발 과정과 마찬가지로 시제품 제작과 보완 과정을 여러 번 반복하면서(최초 시제품을 T1, 이후 T2, T3, T4 등으로 호칭함) 제작하여 제품의 품질(기능과 외관 등)을 충분히 끌어올린 후에야 비로서 제품설계를 확정하고, 대량생산(양산)을 위한 과정을 진행하게 됩니다. 대량생산(양산)을 위한 가장 흔한 과정이 금형 제작 과정입니다. 즉, 저렴한 대량생산을 위한 금형을 제작하는 것이지요. 금형 제작과 더불어 패키지 디자인도 이 단계에서 필요합니다. 금형 제작 과정부터는 사업화 비용이 급격히 늘어나게 되죠. 금형 제작 이후 실제 대량생산에 돌입하게 됩니다. 이러한 방식으로 제품이 시장에 출시되는 것입니다. 대량생산 이후 시장에서 소비자들의 판단을 받게 되죠. 이제부터는 소비자들의 반응 여하에 따라 해당 제품의 성공과 실패 여부가 결정됩니다.

​

좀 더 자세히 살피면, 아이디어를 구체화하여 실제 제품화할 때 다음과 같은 단계를 거치셔야 합니다.

​

(1) 특허출원: 아이디어가 기존에 없는 새로운 구조라면 특허출원을 시도해 볼 수 있습니다. 예를 들어 기존에 존재하는 A기술과 B기술을 결합하여 새로운 제품을 만드는 경우를 말합니다. 여기서 A기술과 B기술을 결합할 때 단순 결합(예: 휴대폰에 거울을 부착한 것으로 휴대폰과 거울의 기능이 전혀 별개로 작동함)이라면 특허를 받기 어렵지만 대부분의 경우 A기술과 B기술을 결합하다보면 단순 결합하기 어렵고 새로운 구조가 나오기 마련입니다(예: 휴대폰 화면의 글자를 잘 보기 위한 확대경 부착 구조). 이러한 부분들에 대해서 특허출원이 가능할 것입니다. 가까운 특허사무소에 연락하셔서 변리사에게 발명의 특허가능성을 검토 받으세요. 대부분 특허가능성 검토 정도는 비용을 받지 않습니다. 특허가능성을 검토 받으실 때는 귀하의 발명 아이디어를 정리한 간단한 설명(A4 1장이면 충분함. 기존 기술 구조에 대한 설명과 기존 기술의 문제점, 특허를 받으시려는 발명의 구조에 대한 설명과 기존 기술과 비교하여 개선된 효과를 기재함) 또는 발명의 구조 도면(손으로 그린 스케치도 가능) 등을 변리사에게 보내시면 검토가 가능합니다.

​​

(2) 기구설계: 특허출원을 할 때에는 아이디어의 개념을 특허문서에서 설명하면 되지만, 아이디어를 제품으로 만들 때는 제품의 각 부품별 기능, 구조, 수치, 재질(플라스틱이나 금속의 경우 수 많은 재질이 존재함), 제조방법 등이 필요합니다. 제품의 기능, 구조, 수치, 재질, 제작방법 등 정하는 것을 보통 '기구설계'라고 하며, 기구설계 과정을 거쳐 제품의 구조가 확정되어야 '시제품' 제작에 들어갑니다. 이러한 '기구설계'는 일반 제조공장에서는 하지 않으며 전문적인 '기구설계' 업체에 의해서 이루어집니다. 일반적인 생산공장에서는 이미 '기구설계'되어 있는 설계도에 따라 대량생산 위주로 생산공정을 진행하기 때문에 시제품 제작 전문 업체가 아니면 샘플 소량 주문을 받지는 않습니다. 인터넷에서 '시제품 제작'을 검색하시면 시제품 제작 업체들을 보실 수 있습니다.

​

(3) 시제품 제작 및 수정: 아이디어를 시제품으로 제작하여 아이디어의 기능 구현이 가능한지와 상업적 성공가능성이 있는지를 검토합니다. 시제품 제작이 1회로 끝나는 경우는 없으며, 시제품 제작과 보완 과정을 여러 번 반복하면서(최초 시제품을 T1, 이후 T2, T3, T4 등으로 호칭함) 제품의 기능과 디자인을 업그레이 한 후 최종적으로 판매할 수준의 제품이 된 경우에 비로서 대량생산(양산)을 위한 금형 제작단계로 넘어가게 됩니다. 참고로 제품이 플라스틱이고 그 구조가 매우 간단한 경우라면 3D 프린팅으로 간단히 시제품을 제작할 수도 있을 것입니다. 인터넷에서 3D 프린팅 검색하시면 업체를 쉽게 발견하실 수 있습니다. 그러나 대부분 3D 프린팅 기술이 아직까지는 정도(정밀도)가 낮은 수준이기 때문에 시제품의 조립 및 기능 구현을 위해 어느 정도의 정밀도가 필요하다면 플라스틱소재를 기계가공(머시닝센터, CNC 등)을 하여 시제품을 제작하시는 것이 바람직합니다.

​

(4) 금형제작: 시제품 제작 과정을 통해 제품의 정확한 구조, 기능, 재질, 제작방법 등이 최종적으로 확정되면, 이제 대량생산을 위한 금형(금속틀)을 제작하는 절차에 돌입합니다. 이러한 금형제작에는 상당히 많은 자금이 일시에 소요되는 것이 일반적입니다. 또한 일단 금형제작을 한 후에는 금형 수정이 매우 어렵기 때문에 금형의 작은 수정은 가능하지만 금형에 대한 전면적인 수정이 필요할 경우에는 다시 금형 제작을 해야 할 경우도 발생할 수 있습니다. 그러므로 시제품 제작 단계에서 시제품을 여러 번 제작하는 과정에서 제품의 구조 등에 대해 철저한 검증이 이루어져야 금형 제작 단계의 비용절감이 가능합니다.  한편, 금형제작 없이 가공만으로 대량생산이 되는 경우도 있습니다(판금, 프레스 가공 등의 제품인 경우).

​

(5) 대량생산: 금형 제작 이후에는 제품 대량 생산단계로 들어갑니다. 대량생산이라는 명칭에서 알 수 있듯이 1번 생산에 최소 5,000~20,000개 정도의 제품을 생산하는 경우가 많습니다. 제품 개당 원가는 생산수량이 클수록 급격히 떨어집니다. 대량생산을 할 때 들어가는 비용항목으로 포장(패키지) 디자인, 조립비(제품을 조립하거나 포장하여 상자에 담는 과정), 포장(패키지) 상자 비용 등이 있습니다. ​​  ​    </pre>
    ​</div>
