  <div class="contents_div_1_title">
    <h3 id="section4" style="margin-top:-6rem; padding-top:6rem;">4. 아이디어에 대한 독점권 - 특허</h3>
  </div>
  <div class="contents_div_1">
    <div class="pre">
      <pre>
귀하께서 소중한 아이디어를 만드셨지만 귀하의 아이디어에 대해 귀하에게 아직 아무런 법적 독점권이 발생하지 않은 상태입니다. 아이디어를 고안하였다는 것 자체로는 권리가 발생되지 않습니다(소설이나 음악과 같은 저작물은 제외함. 저작물은 창작시에 저작권이 발생함). 귀하가 해당 아이디어에 대한 독점권을 가지고 있을 때에만 타인이 귀하에게 해당 아이디어를 구매하거나 로열티를 지급할 이유가 있기 때문에 귀하는 해당 아이디어에 대한 독점권을 가지기 위해 특허를 받으셔야 합니다. 다만, 해당 아이디어가 특허 대상이 아닌 경우에는 사실상 아이디어에 대한 독점권을 가지지 못할 수도 있습니다.

​

시장경제하에서는 아이디어에 대해 독점을 주지 않는 것이 원칙입니다. 특허는 이러한 원칙의 특별한 예외로서 발명 아이디어에 대해 일정한 절차(특허출원)를 거치고 특정요건(진보성-종래기술보다 진보된 것)에 해당되는 경우에 한하여 특허(실용신안권 및 디자인권 포함)를 주는 것뿐입니다. 즉, 일반적으로 특허를 받으려면 "진보성"이 있어야 한다고 말합니다. 즉, 그 발명이 속하는 기술분야에서 통상의 지식을 가진 사람이 기존 기술로부터 쉽게 발명할 수 없다면 특허가 가능합니다(특허요건으로 "진보성"이 필요하다고 함). 구체적으로는 기존 기술과 다른 구조(구성)를 가져야 하고, 그러한 신규 구조로부터 개선된 효과를 가져오는 것이면 특허가 될 수 있습니다. 따라서 귀하의 아이디어가 시중에 전혀 없는 것이라고 하여도 그 자체로 권리가 발생되는 것이 아니라 특허로 등록될 경우에만 보호를 받을 수 있습니다.

​

특허를 받는 것은 타기업이 쉽게 따라하지 못하도록 함에 있습니다. 만약 귀하가 시중에 전혀 없는 상품을 만들어 판매하였을 때 타인이 쉽게 따라서 만들 수 없다면 그러한 기술은 특허로 내지 말고 영업비밀로 간직하고 있어야 합니다. 그러나 대부분의 상품은 귀하가 특허기술로 공개하지 않더라도 동종업자가 귀하가 판매하고 있는 상품을 보고서 어느 정도의 개발과정을 거쳐 만들 수 있기에 타기업이 쉽게 따라하지 못하도록 특허를 받는 것입니다.

​

4-1) 제품 필요 여부

​

특허출원을 진행하기 위한 아이디어의 구체화 정도에 대해 설명드립니다.​

​

특허의 대상인 발명은 '기술적 사상'(기술적 아이디어 자체)이므로, 특허출원을 할 때는 반드시 실제 제품을 제출할 필요가 없습니다. 즉, 특허청에 실제 제품을 제출할 필요없이 특허를 받는 것이 가능합니다.

​

그러나, 특허는 '기술적 사상'(아이디어)이 대상이지만, 실제 특허권으로 보호 받는 것은 기술적 사상 자체가 아니라 '기술적 사상을 구현한 구체적인 구조(방법)'입니다. 즉, 특허는 기존에 국내 또는 외국에 없는 새로운 구조(방법)을 제안하였을 때 부여됩니다.

​

한편, 특허등록을 위한 기술적 사상의 구체화 정도와 관련하여,

발명이 속하는 기술분야에서 보통 정도의 기술적 이해력을 가진 자, 평균적 기술자가 당해 발명을 명세서 기재에 의하여 출원시의 기술수준으로 보아 특수한 지식을 부가하지 않고서도 정확하게 이해할 수 있고 동시에 재현할 수 있는 정도"로 특허출원서를 작성해야 합니다(대법원 97후2477 판결).

​

만약 귀하에게 아이디어를 구체화할 지식이나 경험이 부족하다면 문앤파트너스에서는 운영하는 "시제품 제작을 위한 특허 아이디어 튜닝 프로그램"에 참여해보세요. 귀하의 추상적인 아이디어를 구체화시켜 특허 획득에 도움이 될 것입니다.

​

4-2) 특허출원의 일반적인 흐름도

​

일반적으로 변리사를 통하여 특허문서(실무상 '명세서'라고 함)를 작성하신 후, 특허청에 제출하는 것을 '특허출원'이라고 하며, 특허출원을 제출한 날을 '특허출원일'이라고 합니다. 특허출원을 접수하게 되면 곧바로 '특허출원번호'가 부여됩니다(출원번호통지서에 표기됨). 특허출원할 때 심사청구를 해야 특허청의 심사가 진행되며 만약 특허출원일부터 3년 이내에 심사청구를 하지 않으면 그 특허출원은 취하된 것으로 간주됩니다.

​

통상적으로 특허출원일로부터로부터 대략 16~18개월이 지난 후에 특허청의 심사관의 심사결과통지('의견제출통지서'라고 함)가 송달되며, 이러한 의견제출통지에 대응하여 의견서 및 보정서(특허청구범위를 수정하는 문서)를 제출하게 되며, 특허청 심사관이 관련 자료를 종합 검토하여 특허등록여부를 최종 결정합니다. 일반적으로 특허출원 후 등록 완료까지는 2년(24개월) 이내의 기간이 소요됩니다(우선심사신청을 할 경우는 6~10개월 이내로 등록기간을 당길 수 있으며 우선심사신청비용이 추가로 발생함). 특허출원된 발명이 '특허출원일 기준으로' 국내 또는 외국의 기존기술에 비하여 '진보성(쉽게 말하면 기술적으로 뛰어남)'이 있다면 특허가 등록될 것입니다. 즉, 그 발명이 속하는 기술분야에서 통상의 지식을 가진 사람이 기존 기술로부터 쉽게 발명할 수 없다면 특허가 가능합니다. 구체적으로는 기존 기술과 다른 구조(구성)를 가져야 하고, 그러한 신규 구조로부터 개선된 효과를 가져오는 것이면 특허가 될 수 있습니다. 특허가 등록될 경우 특허번호가 부여되며 특허출원일로부터 20년간 독점권이 발생합니다.

​

4-3) 특허 가능성 검토

​

특허출원 이전에 먼저 귀하의 아이디어가 상업적으로 성공가능한지에 대해 정말 고민하셔야 합니다. 즉, 저희 특허 시제품 제작 경험에 의하면, 특허를 받는다고 하여 실제 시장에서 판매가 잘 되는 것은 아닙니다(소비자가 특허제품이라고 구매하지는 않음). 귀하의 발명 아이디어에 대해 어느 정도 상업적 성공가능성에 대한 확신이 든다면 특허출원을 해야 합니다. 또한 특허출원에는 제법 비용이 들어가므로 먼저 특허가능성도 검토해야 합니다.

즉, 가까운 특허사무소에 연락하셔서 변리사에게 발명의 특허가능성을 검토 받으세요. 대부분 특허가능성 검토 정도는 비용을 받지 않습니다. 방문 상담이나 이메일 또는 전화 상담도 가능합니다. 상담을 통해 발명하신 부분 중에서 특허가 가능한 것이 있는지 아주 상세히 검토를 하셔야 나중에 사업화 과정에서도 권리보호를 충분히 받으실 수 있습니다. 다시 말씀드리지만 특허를 받는데 가장 중요한 것은 담당변리사와의 밀착상담을 통해 특허의 핵심을 이끌어내는 것입니다.

​

또한 특허가능성을 검토 받으실 때는 귀하의 발명 아이디어를 정리한 간단한 설명(A4 1장이면 충분함. 기존 기술 구조에 대한 설명과 기존 기술의 문제점, 특허를 받으시려는 발명의 구조에 대한 설명과 기존 기술과 비교하여 개선된 효과를 기재함) 또는 발명의 구조 도면(손으로 그린 스케치도 가능) 등을 변리사에게 보내시면 검토가 가능합니다.  발명 아이디어에 대한 좀 더 구체적이고 상세한 설명이 필요한 경우라면 변리사와의 대면 상담을 권해드립니다.

​​

4-4) 직접 특허출원 하기

​

특허출원을 진행하는데 있어서 특허문서(명세서)를 잘 작성하는 것이 향후 특허를 등록하는데 있어서 매우 중요하기 때문에 변리사의 도움을 받을 필요가 있습니다. 다만, 특허명세서와 같은 형태로 발명 기술에 대한 설명을 문서로 작성하는데 어려움이 없으신 경우라면 직접 특허출원을 하실 수도 있습니다(이러한 명세서 작성기술은 일정한 노력으로 충분히 학습하실 수도 있음).

​

한편, 특허명세서를 작성하기 위해서는 어느 정도 세부적인 작성 기술을 익힐 필요가 있습니다. 즉, 소설가 지망생들이 유명 작가의 작품을 일일이 필사를 하면서 자신만의 문체를 만들어 나가는 것처럼, 특허문서를 작성하는 기술을 익히는데는 귀하의 일정기간의 노력이 필요합니다.

이하에서 출원인이 직접 특허출원을 신청하는 방법입니다.

​

1) 특허문서의 작성 : 직접 하실 경우, 특허문서를 작성하는 것이 가장 중요합니다. 먼저 특허정보원에서 운영하는 www.kipris.or.kr 사이트를 방문하셔서 특허문서들이 어떻게 작성되었는지 보세요. 귀하의 발명 분야와 비슷한 키워드를 검색하여 이미 등록된 특허문서를 보세요. 그와 유사한 방식으로 특허문서를 작성하세요. 특허문서 작성은 '특허로' 사이트에서 '통합 명세서 작성기'로 작성하셔야 합니다(한글프로그램과 유사함).

2) 도면의 작성 : 특허문서에 첨부할 수 있는 파일 형태는 tiff, jpg 2가지입니다. 실무에서는 캐드 프로그램을 이용해서 tiff 파일의 도면을 첨부합니다만, 일반 이미지 프로그램을 사용하셔서 jpg 형태로 첨부하셔도 됩니다.

3) 특허고객번호 : '특허로' 사이트를 가셔서 귀하의 고유한 특허고객번호를 부여받으세요. 처음 한 번만 받는 것이고, 이후 평생 사용할 수 있는 번호로 주민번호와 유사한 것으로 보시면 됩니다.

4) 특허출원을 위한 프로그램의 다운로드 : '특허로' 사이트에 가시면, 특허문서 작성을 위한 소프트웨어 '통합 명세서 작성기'와 특허문서 제출을 위한 소프트웨어 '서식작성기'가 있습니다. 해당 2가지 소프트웨어를 다운로드 받아서 진행하시면 됩니다.  ​    </pre>
    ​</div>
  </div>
  <div class="contents_div_1_title">
    <h3 id="section5" style="margin-top:-6rem; padding-top:6rem;">5. 특허 라이센스 사업 또는 특허권 매각</h3>
  </div>
  <div class="contents_div_1">
    <div class="pre">
      <pre>한편, 귀하의 아이디어를 타인에게 사용하게 하는 경우에 대한 설명입니다. 특허 아이디어가 제품으로 만들어지는 과정에서 반드시 거쳐야 하는 절차가 제법 많고 불확실성 또한 많기 때문에, 타인이 귀하의 특허권을 매입하거나 귀하가 타인으로부터 로열티를 받으려면 타인에게 '발명 아이디어가 구체화된 제품의 상업적 성공가능성에 대한 확신'을 줄 수 있어야 하겠죠. ​

​

결국 타인에게 귀하의 아이디어 제품의 상업적 성공가능성에 확신을 줄 수 있다면 특허권 매매나 로열티 수령도 가능할 것입니다. 타인에게 귀하 아이디어 제품의 상업적 성공가능성을 주기 위해서는 '특허등록'되었다는 사실만으로는 어렵고, 실제 제품을 만들어 테스트 해보게 해야 하며, 가능하다면 귀하 아이디어 제품의 판매 실적(금액이 작더라도 실제 판매 가능하다는 것을 알려줌)을 보여주는 것이 좋습니다. 저희 특허사업화 자문 경험에 의하면 유감스럽게도 '특허등록증'만으로 특허 라이센스 사업을 할 수는 없습니다.

​

참고로 말씀드리면 앞서와 같이 특허 아이디어가 제품화(판매과정포함)되는 과정이 제법 복잡한 과정을 거치게 되기 때문에 그러한 제품화의 불확실성으로 인하여 타인의 아이디어만을 보고서 선뜻 자신의 비용을 들여서 제조에 들어가는 회사나 투자자가 흔하지 않은 것입니다.

​  ​    </pre>
    ​</div>
  </div>
  <div class="contents_div_1_title">
    <h3 id="section6" style="margin-top:-6rem; padding-top:6rem;">6. 특허를 활용하여 얻을 수 있는 혜택 및 정부지원기관</h3>
  </div>
  <div class="contents_div_1">
    <div class="pre">
      <pre>정부지원사업 지원시 지식재산권 항목에 가산점을 받을 수 있으며, 특허 등을 활용하여 금융권으로부터 대출을 받는 경우도 있습니다. 또한, 기술보증기금이나 신용보증기금에서 사업자금을 빌릴 대 기술력을 입증하는 자료로 특허가 필수적입니다. 나아가 특허 자체가 일종의 독점권이므로 외부 자금의 투자유치시에도 도움이 됩니다.

​

한편, 스타트업을 지원하는 공공기관들이 많이 있습니다. 지원종류에는 대출도 있고, 일부 무상자금지원, 무료 교육 등 서비스지원 등 다양합니다. 각 지원기관별로 지원항목, 자격 또는 지원조건 등이 상이합니다. 따라서 아래 공공기관들을 방문하셔서 귀하에게 맞는 프로그램을 찾아보세요. 어느 정도 발품을 파셔야 합니다. 다만, 지원프로그램의 경우 경쟁률이 높다는 점을 알고계시기 바랍니다. 특히 이제 막 사업자등록을 하고 제품 개발 초기 단계인 경우에는 지원프로그램에 응모하여 선정될 가능성이 상대적으로 많이 높지는 않습니다. 실제 지원대상으로 선정된 업체들을 보면 제품개발단계가 상당히 진행된 업체들이 대부분입니다.

​

국가에서 운영하는 지원기관으로는 중소벤처기업진흥공단(http://www.kosmes.or.kr), 창업진흥원(http://www.kised.or.kr), 소상공인시장진흥공단의 소상공인마당(http://www.sbiz.or.kr/sup/main.do 소상공인 점포창업 지원 위주), 각 테크노파크(http://www.technopark.kr), 각 지역지식재산센터(https://www2.ripc.org 특허 등 지식재산권 등록 지원 전문), K-startup 창업지원사업(k-startup.go.kr 신규 아이디어 창업 지원 전문), 각 도.광역시 지방자치단체, 창업선도대학 프로그램, 한국발명진흥회(http://www.kipa.org), 지역산업종합정보시스템(http://www.rips.or.kr), 팹랩 등 다수가 존재합니다. 또한, 기업마당(http://www.bizinfo.go.kr)와 올댓비즈(http://allthatbiz.korcham.net)와 같은 포털사이트도 활용하실 수 있습니다. 특히 각 시군구 등 지자체에서는 관내 소상공인을 위한 지원프로그램을 많이 운영하고 있습니다. 각 기관의 홈페이지를 수시로 방문하셔서 귀하에게 적합한 지원정책에 응모하세요. 한편, 공모전 정보를 제공하는 위비티(http://www.wevity.com)와 씽굿(http://www.thinkcontest.com)도 참조할 만 합니다.  ​    </pre>
    ​</div>
  </div>
  <div class="contents_div_1_title">
    <h3 id="section7" style="margin-top:-6rem; padding-top:6rem;">7. 전시회 출품</h3>
  </div>
  <div class="contents_div_1">
    <div class="pre">
      <pre>한편, 특허 아이디어를 본인이 직접 사업을 하거나 타인으로 하여금 사용하게 하기 위해서는 '전시회에 출품'하는 것이 가장 효과적인 방법이라고 판단됩니다. 저희 특허 사업화 경험을 통해 보면 대부분의 특허사업화는 특허 발명과 관련된 국내외 전시회를 통해서 제조업체, 유통업체 등과 연결된 경우가 대부분입니다. 즉, 모든 업체들은 항상 새로운 상품을 원하고 있습니다. 그런 업체들이 모두 참여하는 곳이 바로 전시회입니다. 그곳에 참여하셔서 사업 기회를 만들어 보는 것이 좋다고 생각됩니다. 저희 경험상 개별 업체에 접촉하는 것은 그다지 효과적이지는 않습니다. 참고로 국내에서는 삼성동 코엑스(http://www.coex.co.kr), 일산 킨텍스(http://www.kintex.com), 학여울역 세텍(http://www.setec.or.kr) 등에서 업종마다 매년 여러 차례 전시회를 개최하고 있습니다. 뿐만 아니라 일본, 홍콩, 싱가포르, 미국 등지에서도 업종마다 전세계적인 전시회가 열립니다.  ​    </pre>
    ​</div>
  </div>
  <div class="contents_div_1_title">
    <h3 id="section8" style="margin-top:-6rem; padding-top:6rem;">8. 클라우드펀딩</h3>
  </div>
  <div class="contents_div_1">
    <div class="pre">
      <pre>
아이디어를 구현하는 자금을 만드는 방법으로 와디즈와 같은 클라우드 펀딩 업체를 이용하는 방법도 있습니다. 해외의 킥스타터와 같은 사이트도 마찬가지로 활용가능합니다. 다만, 클라우드 펀딩이 가능하려면 실제 제품이 존재해야 하고 동영상 촬영 등 제반 비용이 많이 소요되는 점을 아셔야 합니다. 이러한 클라우드펀딩은 제품에 대한 소비자의 반응을 체크할 수 있다는 장점이 있으나 반대로 자신의 제품을 타인에게 적극적으로 알려서 모방품이 나올 수도 있다는 단점도 가지고 있습니다. ​  ​    </pre>
    ​</div>
  </div>
  <div class="contents_div_1_title">
    <h3 id="section9" style="margin-top:-6rem; padding-top:6rem;">9. 스타트업 창업시 주의사항</h3>
  </div>
  <div class="contents_div_1">
    <div class="pre">
      <pre>스타트업을 포함하는 소상공인의 초기 창업 과정을 자문한 경험을 통해서 저희가 느낀 사업을 시작하실 때 주의하실 점들에 대해 몇 자 적어봅니다.

​

소상공인의 경우 사업을 위한 준비(자금 준비, 마케팅, 운영 등에 대한 기초지식 학습)가 거의 준비없이 사업에 뛰어들기에 생존율이 낮습니다. 만약 사업을 운영하기 위한 기초적인 교육을 받는 경우라면 사업성공가능성이 훨씬 늘어납니다. 따라서 앞서 설명드린 정부지원기관에서 제공하는 교육프로그램들을 적극적으로 수강해보시기 권합니다.

​

한편, 창업 자금 조달에 대해 안내드립니다. 최초 창업 자금을 조달할 때 대출을 고려하는 경우가 많습니다. 창업대출 금액은 창업비용 전체 금액의 50%를 초과해서는 안됩니다(대출없는 것이 최선임). 사업하는 동안 계속해서 이자 비용이 부담이 될 수 있습니다(지금 작게 보이지만 절대 그렇지 않음). 정말 어쩔 수 없이 대출받아야 한다면 최소한만 받으세요. 창업과 폐업은 늘 같은 선상에 있을 수 있다는 점을 명심하시고 자금이 부족하다면 소액창업을 하세요. 한편, 창업 관련 대출 자격으로는 사업자등록 후 3개월 이상 운영해야 하며, 해당 사업자로서의 소득증빙이 되어야 대출이 가능한게 일반적입니다. 다만, 귀하에게 발명 아이디어가 있어서 특허출원 등을 하신 경우라면 소득증빙이 없더라도 기술보증기금의 대출이 가능할 수도 있습니다.

​

또한, 창업의 성공가능성과 관련하여 가장 중요한 것은 아이템의 선정입니다. 아이템의 선정에 정말 고민 많이 하셔야 합니다. 또한 고객이 귀하의 상품을 선택하도록 하는 마케팅을 배우셔야 합니다. 귀하가 준비가 충분히 되셔야 사업에 성공하실 수 있습니다. 창업 관련 각종 교육 기회를 활용하여 내공을 기르시길 바랍니다.​  ​    </pre>
    ​</div>
  </div>
  <div class="contents_div_1_title">
    <h3 id="section10" style="margin-top:-6rem; padding-top:6rem;">10. 공모전 참가</h3>
  </div>
  <div class="contents_div_1">
    <div class="pre">
      <pre>아이디어를 직접 제품화하여 사업에 도전해보는 것이나 아이디어에 대해 특허출원을 하여 특허등록을 받는 절차가 비용 등으로 인해 부담스럽다면 각종 공공기관이나 기업에서 주최하는 아이디어 공모전에 참가하세요. ​

​

공모전 정보를 제공하는 위비티(http://www.wevity.com)와 씽굿(http://www.thinkcontest.com)를 방문하여 귀하가 제안하기 적합한 공모전을 검색해보세요. 공모전에 따라서는 상금이나 상품이 있는 경우도 많고 귀하에게 해당 아이디어 사업화 기회를 부여할 수도 있습니다.

​​

​

감사합니다.

​

