divd<template>
  <div style="float:left;">
    <router-link to="/home" ><img :src="imgLogo" style="width: 157px; height: 70px;"></router-link>
  </div>
    <div class="menu_container" v-if="menu">
        <button v-on:click ="showMenuAdmin();" v-if="this.isAdminYn == 'Y'">관리자</button>
        <div class="menu_box" v-for="(menuItem,id) in menuItem" :key="id">
          <div class="top_menu_element">
            <router-link class="router_link" v-on:click="setMenu(id, -1);" :to="{path:menuItem.router}">{{menuItem.names}}</router-link>
          </div>
          <div class="sub_menu_element" v-for="(item,sub_id) in menuItem.subMenuItem" :key="sub_id">
            <router-link class="router_link" v-on:click="setMenu(id, sub_id);" :to="{path:item.router}">{{item.names}}</router-link>
          </div>
        </div>
    </div>
    <!-- admin menu -->
    <div class="menu_container" v-if="menu_admin">
      <button v-on:click ="showMenu();" v-if="this.isAdminYn == 'Y'">사용자</button>
      <div class="menu_box" v-for="(menuAdminItem,id) in menuAdminItem" :key="id">
        <div class="top_menu_element">
          <router-link v-on:click="setMenu(id, -1);" :to="{path:menuAdminItem.router}">{{menuAdminItem.names}}</router-link>
        </div>
        <div class="sub_menu_element" v-for="(item,sub_id) in menuAdminItem.subMenuItem" :key="sub_id">
          - <router-link v-on:click="setMenu(id, sub_id);" :to="{path:item.router}">{{item.names}}</router-link>
        </div>
      </div>
    </div>
  
  userId : {{ this.userId }}
  
  <div class="top_login" v-if="!this.loginSuccess">
    <div style="margin:auto; float:left;"><input type="button" value="회원가입"></div>
    <div style="margin:auto; float:left;"><input type="button" value="로그인" v-on:click="modalOpen('A');"></div>
  </div>
  <div class="top_login" v-else>
    <div style="margin:auto; float:left;">어서오세요, {{this.userInfo}}님</div>
  </div>
  <!-- modal -->
  
  <div class="modal" v-if="showModal">
    <div class="modal_white_bg">
      <div class="contents_div_1" v-if="regLoginModal">
        <div class="contents_div_1_title">
          로그인
          <div style="border:0px solid red; float: right; margin-right:5rem;">
              <input type="button" class="button-close" value="" @click="modalClose(true)">
          </div>
        </div>
        <div class="contents_div_1">
          <div style="width:95%; margin:auto;">
            아이디 : <input type="text" v-model="loginId">
            비밀번호 : <input type="text" v-model="loginPw">
          </div>
          <div style="width:15rem; height:3rem; argin:auto; border-radius: 10px; box-shadow: 0.3rem 0.3rem 0.3rem #BFBFBF; display: flex;
                      justify-content : center; align-items: center;  background-color: #AEB555; font-size:2rem;" v-on:click="doLogin();">
            로그인
          </div>
        </div>
      </div>
      <div class="contents_div_1" v-if="regUserRegModal">
        <div class="contents_div_1_title">
          회원가입
          <div style="border:0px solid red; float: right; margin-right:5rem;">
              <input type="button" class="button-close" value="" @click="modalClose(true)">
          </div>
        </div>
        <div class="contents_div_1">
          <div style="width:95%; margin:auto;">
            회원가입 페이지
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const axios = require("axios");
export default {
  name: 'Top',
  data() {
    return {
      loginId: 'myplant',
      loginPw: 'myplant',
      loginSuccess: false,
      isAdminYn: 'N',
      userInfo: null, 
      userId: null,
      showModal: false,
      regLoginModal: false, 
      regUserRegModal: false, 
      imgLogo: require("@/assets/images/common/logo_long_v1.jpg"),
      sub_id: '', 
        // 사용자 우선
        menu: true, 
        menu_admin: false,
        
        // 관리자 우선
        // menu: false, 
        // menu_admin: true,

        // manu
        menuId: 0, 
        id: 0, 
        subId: 0, 
        
        menuAdminItem: [],
        menuItem: [],

    }
  },
  beforeMount() {
    this.getMenuAuth();
  },
  methods: {
    doLogin(){
      var userJson;
      var loginSuccess = false;

      console.log('contentsId : ', 0, ' / loginId : ', this.loginId, ' / loginPw : ', this.loginPw);
      axios
        .post("/api/user/user", {contentsId: 0, loginId: this.loginId, loginPw: this.loginPw})
        .then((response) => {
          userJson = response.data[0].get_result_json;
          // console.log('userJson[0].login_id : ', userJson[0].login_id);       
          console.log('userJson[0] : ', userJson[0]);
          
          if(this.loginId==userJson[0].login_id) {
            loginSuccess = true;
          }
          console.log('loginSuccess ++++ ', loginSuccess);
          
          if(loginSuccess = true) {
            alert('success!!');
          } else {
            alert('login fail');
          }
          
          this.userInfo = userJson[0].login_id;
          this.userId = userJson[0].usr_id;
          this.isAdminYn = userJson[0].isAdminYn;

          console.log('in Top.vue : this.loginId ::: ', this.userInfo);

          // this.$parent.getLoginInfo(1);
          this.modalClose('Y')

          this.getMenuAuth();
        })
        .catch((error) => {
        console.log(error);
        });
    },
    async getMenuAuth() {
      await axios
          .post("/api/admin/menu", { contentsId: 25, usrId: this.userId })
          .then((response) => {
              var resultJson;
      
              resultJson = response.data;
              console.log('menu.js : resultJson ::: ', resultJson);
              
              if (this.isAdminYn == 'Y') {
                this.menuAdminItem = resultJson[0].get_result_json[0];
                this.menuItem = resultJson[0].get_result_json[1];
              } else 
                this.menuItem = resultJson[0].get_result_json[0];
              
              console.log('this.menuAdminItem ::: ', this.menuAdminItem);
              console.log('this.menuItem ::: ', this.menuItem);

          })
          .catch((error) => {
              console.log(error);
          });
      },
    showMenu(){
      // alert('showMenu');
      this.menu=true;
      this.menu_admin=false;
    }, 
    showMenuAdmin(){
      // alert('showMenuAdmin');
      this.menu=false;
      this.menu_admin=true;
    },
    setMenu(id, subId){
      this.id = id;
      this.subId = subId;
    }, 
    modalOpen(modalGbn){
      this.showModal = true;
      if(modalGbn == 'A') {
        this.regLoginModal = true;
      } else if(modalGbn == 'B'){
        this.regUserRegModal = true;
      }
    }, 
    modalClose(refreshYn) {
      this.showModal = false;
      // if(refreshYn) {
      //   this.setUserInfo();
      // }
    },
  }
}
</script>
<style scoped>
</style>

