<template>
  <div class="contents_div_1">
    <table>
      <tr>
        <td>
          <div style="border:1px solid red; border-radius: 10px; width=50%;">
            <h6>[Menu5-3.vue]</h6>    
            한 해 살이 식물 다 키우고, 흙이 너무 많이 남았어요. 어떻게 재활용할까요? <br>
            실제로 농사 지으시는 분들은 흙을 ㅇㅇㅇㅇ 게 합니다. <br>
            그러나, 집에서 키우시는 분들은.. <br>
            blog 에 자세한 내용을 기록해 놓았습니다. <br>
            <br>
            Energy Harvesting 기술들을 나열한다. 방문자들은 자신만의 계정을 만들고 이 기술들을 선택, 선택하면 어떤 것이 가능한지 가상의 환경을 보여준다.<br>
            내부 엔진에서 이를 돌려서 보여준다. ( 태양광 + 농업 => 스마트팜.. 뭐 이런 식..;;)<br>
            <iframe width="120" height="90" src="https://www.youtube.com/embed/A8qTRBc8Bws" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
          </div>
        </td>
        <td sytle="width=70%">
          <div style="border:1px solid red; border-radius: 10px; width=50%;">
            <!-- 조회form -->
            <div class="container-column" v-if="viewMode">
              <input type="button" class="button-board" value="목록으로" v-on:click="this.viewMode=false; this.listMode=true;" />
              <input type="text" class="input-text" readonly="readonly" v-model="this.title" />
              <textarea class="textarea-basic" cols="30" rows="20" readonly="readonly" v-model="this.contents" />
            </div>
            <div>
              <table>
                <thead>
                  <tr>
                    <th v-for="(a, i) in resultKey" :key="i">{{ this.resultKey[i] }}</th>
                  </tr>
                </thead>
                <tbody>
                  <!-- <tr v-for="(row,rid) in resultArr" :key="rid" v-on:click="viewData({{row.heal_act_id}});" style="cursor:pointer;"> -->
                  <tr
                    v-for="(row, rid) in resultArr"
                    :key="rid"
                    v-on:click="viewData(row.heal_act_id)"
                    style="cursor: pointer"
                  >
                    <td v-for="(col, cid) in row" :key="cid" v-text="col"></td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="row">...페이지...</div>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import Menu5_Nav from './Menu5_Nav';
var contents_id;

export default {
  components: {
            Menu5_Nav: Menu5_Nav
        },
  data() {
    return {
      contents_id, 
      tagId: "0",
      listMode: true,
      viewMode: false,
      title: "",
      contents: "",
      resultArr: [],
      resultKey: [],
      }
  },
  created() {
    this.contentsList('친환경');
  },
  mounted() {
    // console.log(this.img);
  },
  methods: {
    contentsList(tagName) {
      this.listMode = true;
      this.viewMode = false;
      
      const axios = require("axios");
      axios
        // .post("/api/healActBoard", { dml: '', contentsId: "9" })
        .post("/api/healActBoard", { dml: '', contentsId: 13, tag: tagName })
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          this.resultJson = response.data;
          this.resultArr = this.resultJson[0].get_result_json;
          this.resultKey = Object.keys(this.resultArr[0]);
          // delete this.resultKey[2];
        })
        .catch((error) => {
          console.log(error);
        });
      this.healActId = null;
    },
    viewData(healActId) {
      this.listMode = false;
      this.viewMode = true;

      var resultJson;
      var resultArr;
      var resultKey;

      const axios = require("axios");
      axios
        .post("/api/healActBoard", { dml: '', healActId: healActId })
        .then((response) => {
          resultJson = response.data;
          resultArr = resultJson[0].get_result_json;
          // resultKey = Object.keys(resultArr[0]);
          
          this.title = resultArr[0].title;
          this.contents = resultArr[0].contents;
          this.healActId = healActId;
          // console.log('this.title = ', this.title);
        })
        .catch((error) => {
          console.log(error);
        });
        this.listMode=false;
    },    
  },
}
</script>

