<template>
    <div class="contents_div_1">
        <h6>검색 기능 개발 중</h6>
        <div class="container-row">
            <!-- Search Componenet -->
            <div class="boxes-small" v-for="item in searchList" v-bind:key="item.id">
                <input type="radio" name="stdSearch" id="{{item.id}}" v-on:change="setData(item.contentsId);">
                <label for="{{item.id}}">{{item.names}}</label>
            </div>
        </div>
        <div class="container-row">
            <!-- <v-text-field v-model="searchWord" dense outlined label="검색키워드" full-width :style="{marginTop:'10px'}"/> -->
            <!-- <input type="submit" value="검색" v-on:click="searchData(pInput)"> -->
            <!-- <input type="text" v-model="paramInput" placeholder="검색어" /><button v-on:click="searchData(paramInput)">검색</button> -->
            <input type="text" placeholder="검색어를 입력해주세요" v-model="pInput" /><button v-on:click="searchData(pInput)">검색</button>
        </div>
        <div class="row">
            <table>
            <thead> 
                <tr>
                <th v-for="(a,i) in resultKey" :key="i">{{ this.resultKey[i] }}</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(row,rid) in resultArr" :key="rid">
                <td v-for="(col,cid) in row" :key="cid" v-text="col"></td>
                </tr>
            </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import resultJsonImp from "../Menu7/resultVal.json"; // 원천데이터1

var resultJson;
var pInput;
var resultArr; 
var contentsId;
var searchWord;

export default {
    components: {
        },
    data() {
            return {
                searchList: [
                    {names: '표준단어검색', id: 1, contentsId: 7},
                    {names: '표준용어검색', id: 2, contentsId: 8},
                ],
                resultJson,
                resultArr: [],
                resultKey: [],
                
                pInput,
                resData: [],
                contentsId,
                searchWord,
            }
        },
    mounted() { 
    },  
    methods: {
        setData(contentsId){
            this.contentsId = contentsId;
        },
        searchData(pInput){
            // console.log('pInput : ', pInput);
            if(pInput == '') {
                alert('키워드가 없습니다!');
            } else {
                const axios = require('axios');
                axios.post('/api/da_std_word_search', {contentsId: this.contentsId, searchWord: pInput})
                        .then(response => {
                            // console.log('Test.vue::',response.data);
                            this.resultJson = response.data;
                            // console.log('Test.vue::',response.data);
                            this.resultArr = this.resultJson[0].get_result_json;
                            this.resultKey = Object.keys(this.resultArr[0]);
                        })
                        .catch(error => {
                            console.log(error)
                        })
            }
        },
    },  
}



</script>

<style>
/* @import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap"); */
</style>