<template>
<div class="modal" v-if="modalOn">
    <div class="modal_white_bg">
        <div style="width:80rem; height:40px; margin:auto; padding-left:10px; border:0px solid blue;">
            {{ this.modalTitle }}
            <div style="border:0px solid red; float: right; margin-right:5rem;">
                <input type="button" class="button-close" value="" @click="modalClose(true)">
            </div>
        </div>
        <div style="width:80rem; height:40px; margin:auto; padding-left:10px; border:0px solid blue;">
            <div style="float:left; margin-left:1rem;">
                <input type="checkbox" v-model="changeTimeYn" @click="changeTimeYn=true" values="날짜/시간 변경">날짜/시간 변경
            </div>
            <div style="float:left; margin-left:1rem;" v-if="changeTimeYn">
                <input type="date" v-model="recrdDt" min="2020-01-01" max="9999-12-31" v-on:change=setRecrdDttm($event)>
                <input type="time" v-model="recrdTime" min="2020-01-01" max="9999-12-31" v-on:change=setRecrdDttm($event)>
            </div>
            <div style="float:left; margin-left:1rem;">
                recrdDt:{{ this.recrdDt }} / 
                recrdTime:{{ this.recrdTime }} / 
                timestamp:{{ this.timestamp }} / 
                record dttm : {{ this.recrdDttm }}
            </div>
        </div>
        <!-- 추가/삭제 -->
        <div style="width: 90%; margin: auto; margin-top:10px; border: 0px solid red;" v-if="showModal">
            <table style="border: 0px solid red;">
                <td style="width:48%; text-align:center; border: 0px solid orange;">
                    추가할 식물들
                    <div style="margin:auto; margin-top:1rem; overflow-y: scroll; border: 1px solid green;">
                        <table>
                            <tr style="height:2rem;">
                                <td style="width: 1rem;"></td>
                                <td style="width: 7rem">작물명</td>
                                <td style="width: 4rem;">반려식물 이름</td>
                                <td style="width: 3rem;">반려식물사진</td>
                            </tr>
                            <tr v-for="(item, index) in cropJson" style="height:2rem;">
                                <td style=""><input type="checkbox" v-model="selectArr[index]"></td>
                                <td style="padding-left:0.5rem; text-align: left;">   
                                    {{ this.cropJson[index].crp_nm }}
                                </td>
                                <td style="padding-left:0.5rem; text-align: left;">
                                    <input type="text" v-model="this.cmplntNm[index]">
                                </td>
                                <td style="padding-left:0.5rem; text-align: left;">
                                    <div style="width:95%; margin:auto;">
                                        <!-- <input style="display: none" type="file" @change="onFileSelected($event)" ref="setImg" /> -->
                                        <input type="file" @change="onFileSelected($event)" ref="setImg" />
                                    </div>
                                    <!-- <div style="border: 0px solid DarkOliveGreen;">
                                        <button @click="$refs.setImg.click">Image Pick</button>
                                    </div> -->
                                    
                                    <!-- <input type="button" value="선택"> -->
                                    <!-- <input type="button" class="" value="선택" @click=""> -->
                                </td>
                            </tr>
                        </table>
                    </div>
                    <div>
                        <!-- <router-link v-on:click="setMenu(id, -1);" :to="{path:menuItem.router}">{{menuItem.names}}</router-link> -->
                        <!-- <router-link :to="'/menu2_3'"> -->
                            <input type="button" class="button-img" value="추가" @click="myCrpAdd($event)">
                        <!-- </router-link> -->
                    </div>
                </td>
                <td style="width: 48%;">
                    내 반려식물 목록들
                    <div style="margin:auto; margin-top:1rem; overflow-y: scroll; border: 0px solid green;">
                        <table>
                            <tr style="height:2rem;">
                                <td style="width: 1rem;"></td>
                                <td style="width: 7rem">작물명</td>
                                <td style="width: 7rem;">반려식물 이름</td>
                            </tr>
                            <tr v-for="(item, index) in myPlantArr" style="height:2rem;">
                                <td style=""><input type="checkbox" v-model="myCrpDelArr[index]"></td>
                                <td style="padding-left:0.5rem; text-align: left;">
                                    {{ this.myPlantArr[index].crp_nm }}
                                </td>
                                <td style="padding-left:0.5rem; text-align: left;">
                                    {{ this.myPlantArr[index].cmplnt_nm }}({{ this.myPlantArr[index].record_cnt }})
                                </td>
                            </tr>
                        </table>
                    </div>
                    <div>
                        <input type="button" class="button-img" value="삭제" @click="myCrpDel($event)">
                    </div>
                </td>
            </table>
        </div>
        <!-- 재배기록 -->
        <div style="margin-top:10px; border: 1px solid black;" v-else-if="regRecordModal">
            <div style="width:95%; margin-left:3rem; margin:1rem;">
                <input type="checkbox" v-model="questionYn" />질문카페에 등록하기
                <textarea class="textarea-basic" cols="100" rows="1" 
                            style="resize: none" v-if="questionYn" placeholder="궁금한 내용을 입력하세요." />
                <textarea class="textarea-basic" cols="100" rows="1" 
                            style="resize: none" v-else disabled />
            </div>
            <div style="width:95%; margin:auto;">
                <table>
                <thead>
                    <tr>
                    <th class="th-UserActionMemo">활동</th>
                    <th class="th-UserActionMemo">내용</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in cltvtAct" v-bind:key="item.id">
                    <td class="td-content">
                        <input type="checkbox" value="item.cltvtActCd" v-model="inputUsrRecordArr[item.id]" />{{ item.cltvtActNm }} {{ item.cltvtActCd }}
                    </td>
                    <td class="td-content">
                        <textarea class="textarea-basic" cols="100" rows="1" 
                                    style="resize: none" v-model="arrMemo[item.id]" 
                                    v-if="inputUsrRecordArr[item.id]" placeholder="활동 내용 또는 간단한 메모를 입력하세요." />
                        <textarea class="textarea-basic" cols="100" rows="1" 
                                    style="resize: none" v-model="arrMemo[item.id]" v-else disabled />
                    </td>
                    </tr>
                    <tr>
                    - 수경재배인 경우, 물주기가 아니라 물 갈아주기로 설정.<br />
                    </tr>
                </tbody>
                </table>
            </div>
            <div style="width:fit-content; margin:auto;">
                <button class="button_green_square" v-on:click="activitySave()">활동 저장</button>
            </div>
        </div>
        <!-- 육아일기 쓰기 -->
        <!-- <div style="border: 0px solid DarkOliveGreen;" v-else-if="regDiaryModal"> -->
        <div class="contents_div_1" v-else-if="regDiaryModal">
            <div style="width:95%; margin:auto;">
                <input style="display: none" type="file" @change="onFileSelected($event)" ref="fileInput" />
            </div>
            <div style="border: 0px solid DarkOliveGreen;">
                <table style="border: 1px solid red;">
                <tr>
                    오늘은 파종 후 10일차 되는 날입니다.
                </tr>
                <tr>
                    <td>
                    <img :src="imageSrc" v-if="imageSrc" style="width: 200px; height: 200px; object-fit: contain;"/><br>
                    <button @click="$refs.fileInput.click">Image Pick</button>
                    </td>
                    <td>
                    <textarea v-model="imgDiaryCntnts" class="textarea-basic" cols="30" rows="10" 
                                style="resize: none" placeholder="활동 내용 또는 간단한 메모를 입력하세요." />
                    <button v-on:click="imageSave()">일기저장</button>
                    </td>
                </tr>
                </table>
            </div>
        </div>
        <!-- 문의하기 -->
        <div style="border: 0px solid DarkOliveGreen;" v-else-if="regQuestionModal">
            질문 내용
            [진단의뢰][관련사진] [전문가 답변]
            서비스 참고) https://www.nongsaro.go.kr/portal/ps/pss/pssb/ncpmsConsultDtl.ps?menuId=PS65440&pageIndex=1&dgnssReqNo=5512&sRealmCode=&sSidoCode=&sSigunguCode=&srchStr=
        </div>
        <!-- 반려식물 프로필 수정 -->
        <div style="border: 0px solid DarkOliveGreen;" v-else-if="editProfileModal">
            <div class="contents_div_2">
                <img :src="fileUrl" class="image_card" />
            </div>
            <div class="contents_div_2">

                <input style="display: none" type="file" @change="onFileSelected($event)" ref="fileInput2" />
                
                <div style="width:95%; margin:auto;">
                    <img :src="imageSrc" v-if="imageSrc" style="width: 200px; height: 200px; object-fit: contain;"/><br>
                    <button @click="$refs.fileInput2.click">Image Pick</button>
                    <button v-on:click="myPlantProfileImageUpdate()">사진수정</button>
                </div>
            </div>

        </div>
    </div>
</div>

</template>
<script>
const axios = require("axios");
export default {
    components: {
    },
    props : {
        usrId: Number, 
        cropJson: Object,
        myPlantArr: Object, 

        cltvtAct: Object,
        cltvtId: Number,
        
        fileUrl: String,
    },
    data() {
        return {
            modalOn: false, 
            modalTitle: false,

            showModal : false,
                selectArr: [], 
                myCrpDelArr: [],
                cmplntNm: [],
            regRecordModal : false,
                inputUsrRecordArr: [],
                arrMemo: [],
            regDiaryModal : false,
            regQuestionModal : false,

            imageSrc: null,
            imgDiaryCntnts: null,
            selectedFile: null,

            // cltvtId: null, 
            cltvtActCd: null, 
            recrdDttm: null, 
            recrdDt: null,
            recrdTime: null, 

            timestamp: null, 
            changeTimeYn: false,

            questionYn: false,
        }
    },
    mounted () {
        // this.updateNow();
        setInterval(this.updateNow.bind(this) , 1000);
    },
    methods: {
        updateNow(){
            var now = new Date();
            // console.log('now : ', now);

            var year = now.getFullYear();
            var month = ('0' + (now.getMonth() + 1)).slice(-2);
            var day = ('0' + now.getDate()).slice(-2);

            var hours = ('0' + now.getHours()).slice(-2); 
            var minutes = ('0' + now.getMinutes()).slice(-2);
            var seconds = ('0' + now.getSeconds()).slice(-2); 

            var dateString = year + '-' + month  + '-' + day;
            var timeString = hours + ':' + minutes  + ':' + seconds;

            this.timestamp = dateString + ' ' + timeString;

            if (this.recrdDt == null) {
                this.recrdDt = dateString;
            }
            if (this.recrdTime == null) {
                this.recrdTime = timeString;
            }

            this.recrdDttm = this.recrdDt + ' ' + this.recrdTime;
            // alert(this.recrdDttm);
        },
        setVal(modalNm){
            this.modalOn = true;

            if(modalNm == 'showModal') {
                this.modalTitle = "나의 식물 추가";
                this.showModal = true;
            } else if(modalNm == 'regRecordModal') {
                this.modalTitle = "재배 기록";
                this.regRecordModal = true;    
            } else if(modalNm == 'regDiaryModal') {
                this.modalTitle = "육아 일기";
                this.regDiaryModal = true;
            } else if(modalNm == 'regQuestionModal') {
                this.modalTitle = "문의하기";
                this.regQuestionModal = true;
            } else if(modalNm == 'editProfileModal') {
                this.modalTitle = "나의 식물 프로필 수정";
                this.editProfileModal = true;
            }
        },
        myCrpAdd(){
            var crpCltvtRelJson = {};
            var crpCltvtRelJsonArray = [];
            console.log("this.selectedFile : ", this.selectedFile);
            //  // 1. 사용자 입력 내용 확인
            //  cltvtActJson = {
            //     cltvtId: this.cltvtId,
            //     cltvtActCd: 800,
            //     imgDiaryCntnts: this.imgDiaryCntnts,
            //     recrd_dttm: this.recrdDttm,
            //     // : this.imgDiary,
            //     // cntnts: this.arrMemo[i],
            // };

            // 2. 반려식물 이미지 
            for (let i in this.selectArr) {
                if(this.selectArr[i]){
                    crpCltvtRelJson = {
                        usr_id: this.usrId,
                        crp_id: this.cropJson[i].crp_id,
                        cmplnt_nm: this.cmplntNm[i],
                        cmplnt_img_path: this.cmplntNm[i],
                    };
                    crpCltvtRelJsonArray.push(crpCltvtRelJson);
                }
            }
            
            const formData2 = new FormData();
            formData2.append("dml", "insert");
            formData2.append("crpCltvtRelJson", JSON.stringify(crpCltvtRelJson));
            formData2.append("image", this.selectedFile);

            axios
                // .post("/api/cultivate", {dml: 'insert', usrId:this.usrId, crpCltvtRelJson: crpCltvtRelJson, crpCltvtRelJsonArray: crpCltvtRelJsonArray})
                .post("/api/cultivate", formData2)
                .then((response) => {
                    console.log('response.data ///////////++++ ', response.data);
                    alert('당신의 반려식물이 추가되었습니다.');
                })
                .catch((error) => {
                console.log(error);
                });
            this.modalClose(true);

        },
        myCrpDel(){
            var crpCltvtRelJson = {};
            var crpCltvtRelJsonArray = [];
            // console.log('myCrpDelArr : ', this.myCrpDelArr);
            for (let i in this.myCrpDelArr) {
                if(this.myCrpDelArr[i]){
                    crpCltvtRelJson = {
                        cltvt_id: this.myPlantArr[i].cltvt_id,
                    };
                    crpCltvtRelJsonArray.push(crpCltvtRelJson);
                }
            }
            axios
                .post("/api/cultivate", {dml: 'update', crpCltvtRelJsonArray: crpCltvtRelJsonArray})
                // axios.post(this.router, resultJsonImp)
                .then((response) => {
                    console.log('response.data ///////////++++ ', response.data);
                    alert('당신의 반려식물이 삭제되었습니다.');
                })
                .catch((error) => {
                console.log(error);
                });
            this.modalClose(true);
        },
        setRecrdDttm(e){
            // alert(e.target.value);

            if (this.changeTimeYn) {
                this.recrdDttm = this.recrdDt + ' ' + this.recrdTime;
            } else {
                this.recrdDttm = this.timestamp;
            }
        },
        activitySave(e) {
            // 재배 활동 기록 내용 저장.
            var cltvtActJson = {};
            var cltvtActJsonArray = [];
            var resultMsg;

            // 1. 사용자 입력 내용 확인
            for (var i in this.cltvtAct) {
                if (this.inputUsrRecordArr[i]) {
                cltvtActJson = {
                    cltvtId: this.cltvtId,
                    cltvtActCd: this.cltvtAct[i].cltvtActCd,
                    cntnts: this.arrMemo[i],
                    recrd_dttm: this.recrdDttm,
                };
                cltvtActJsonArray.push(cltvtActJson);
                }
            }

            // 2. 저장
            // user record save
            axios
                .post("/api/plant", { dml: 'insert', cltvtActJsonArray: cltvtActJsonArray})
                .then((response) => {
                    resultMsg = response.data.result;
                    console.log('resultMsg : ',resultMsg);

                    alert('반려식물 기록이 추가되었습니다.');
                })
                .catch((error) => {
                    console.log(error);
                });

                // window.location.href = "/menu2_3"; // ????
                this.inputUsrRecordArr = [];
                this.arrMemo = [];
                // this.$emit('refresh');
                // this.$router.push({path : '/menu2_3', name:'menu2_3', params: {cltvtId: this.cltvtId}});

            this.modalClose(true);
        },
        onFileSelected: function (e) {
            
            // 1. 첨부할 파일을 가져옴.
            this.selectedFile = e.target.files[0];
            ///// validation (이미지인지? 사이즈 제한? )

            // 2. 파일을 base64 로 전환
            var reader = new FileReader();
            reader.readAsDataURL(this.selectedFile);

            // 3. 읽은 파일을 소스단에 출력
            reader.onload = () => {
                // console.log(reader.result);   // base64 object
                this.imageSrc = reader.result;
            };
            // this.$emit('input', this.selectedFile)
            console.log('selectedFile >>>>>>>> ', this.selectedFile);
        },
        imageSave(){
            var cltvtActJson = {};
            var cltvtActJsoArray = [];
            // console.log('this.imgDiaryCntnts ::: ', this.imgDiaryCntnts);
            
            // recrdDttm: null, 
            // recrdDt: null,
            // recrdTime: null, 
            if (this.changeTimeYn) {
                this.recrdDttm = this.recrdDt + ' ' + this.recrdTime
            } else {
                this.recrdDttm = this.timestamp;
            }
            // else {
            //     this.recrdDttm = 
            // }
            
            console.log(this.recrdDttm);

            // 1. 사용자 입력 내용 확인
            cltvtActJson = {
                cltvtId: this.cltvtId,
                cltvtActCd: 800,
                imgDiaryCntnts: this.imgDiaryCntnts,
                recrd_dttm: this.recrdDttm,
                // : this.imgDiary,
                // cntnts: this.arrMemo[i],
            };

            // 2. 반려식물 이미지 
            const formData = new FormData();
            formData.append("diaryYn", "Y");
            formData.append("image", this.selectedFile);
            formData.append("cltvtActJson", JSON.stringify(cltvtActJson));
            
            axios
                .post("/api/file/uploadImage", formData)
                .then((response) => {
                    console.log(response.status);
                    alert('반려식물 일기가 저장되었습니다.');
                    this.modalClose(true);
                })
                .catch((err) => {
                    console.log("uploadImage post error");
                    console.log(err);
                });

                // window.location.href = "/menu2_3";
        },
        myPlantProfileImageUpdate(){
            var cltvtActJson = {};
            var cltvtActJsoArray = [];

            // 1. 사용자 입력 내용 확인
            cltvtActJson = {
                cltvtId: this.cltvtId,
            };

            // 2. 반려식물 이미지 
            const formData = new FormData();
            formData.append("profileYn", "Y");
            formData.append("image", this.selectedFile);
            formData.append("cltvtActJson", JSON.stringify(cltvtActJson));
            
            axios
                .post("/api/file/uploadImage", formData)
                .then((response) => {
                    console.log(response.status);
                    alert('반려식물 프로필 사진이 변경되었습니다.');
                    this.modalClose(true);
                })
                .catch((err) => {
                    console.log("uploadImage post error");
                    console.log(err);
                });

                // window.location.href = "/menu2_3";
        },
        // imageDelete(){
        //     axios
        //         .post("/api/file/uploadImage", { dml: 'delete'
        //                                         , cltvtId: this.cltvtId
        //                                         , cltvtActCd: this.cltvtActCd
        //                                         , recrdDttm: this.recrdDttm})
        //         .then((response) => {
        //             console.log(response.status);
        //             alert('반려식물 일기가 삭제되었습니다.');
        //         })
        //         .catch((err) => {
        //             console.log("uploadImage post error");
        //             console.log(err);
        //         });

        //         // this.modalClose(true);
        // },
        modalClose(refreshYn) {
            this.modalOn = false;

            if(this.showModal) {
                // 잠깐 닫았을 때, 기록했던 내용 없앰. (만약 유지가 필요하다면, 이 부분은 제거.)
                this.selectArr = [];
                this.cmplntNm = [];
                this.myCrpDelArr = [];
                this.showModal = false; 

                if(refreshYn) {
                    this.$parent.refreshMyPlantList(this.usrId, this.cltvtId);
                }
            } else if(this.regRecordModal) {
                // 잠깐 닫았을 때, 기록했던 내용 없앰. (만약 유지가 필요하다면, 이 부분은 제거.)
                this.inputUsrRecordArr = [];
                this.arrMemo = [];
                // 모달 창 닫기
                this.regRecordModal = false;

                if(refreshYn) {
                    this.$parent.refreshRecord(this.usrId, this.cltvtId);
                }
            } else if(this.regDiaryModal) {
                // 잠깐 닫았을 때, 기록했던 내용 없앰. (만약 유지가 필요하다면, 이 부분은 제거.)
                this.imgDiaryCntnts = '';
                this.imageSrc = '';
                // 모달 창 닫기
                this.regDiaryModal = false;

                if(refreshYn) {
                    // alert(this.cltvtId);
                    this.$parent.refreshDiary(this.usrId, this.cltvtId);
                }
            } else if(this.regQuestionModal) {
                this.regQuestionModal = false;
            } else if(this.editProfileModal) {
                this.editProfileModal = false;

                // 변수 초기화
                this.imageSrc = "";
                
                if(refreshYn) {
                    // alert(this.cltvtId);
                    this.$parent.refreshProfile(this.usrId, this.cltvtId);
                }
            }
        },
    }
}

</script>
<style>
</style>