<script>
// import { thisExpression } from '@babel/types';
// import Menu2_3_Cltvt from "./Menu2_3_Cltvt";
import { throwStatement } from "@babel/types";
import Menu2_3_Image from "./Menu2_3_Image";
import MyPlant from './Menu2_3_MyPlant';

const axios = require("axios");
var myPlantArr;
var myPlantRecordArr;
var myRecordsArr;
// var wikiSearchUrl = "https://ko.wikipedia.org/wiki/";

export default {
  name: "MainComponent",
  components: {
    Menu2_3_Image: Menu2_3_Image,
    // Menu2_3_Cltvt: Menu2_3_Cltvt,
    MyPlant: MyPlant,
  },
  props : {
    userId: String, 
  },
  data() {
    return {
      
      usrId: null,
      cltvtId: null, 
      cmplntNmEdit: false,
      profileImg: null,

      modalOn: false,
      showModal: false,
      regRecordModal: false,
      regDiaryModal: false,
      regQuestionModal: false,
      cropJson: null,

      file_url: null,
      frameImg: require("@/assets/images/common/frame-g3e627c4ba_300.png"),
      noImg: require("@/assets/images/common/no_image.png"),
      buttonImg: require("@/assets/images/common/button_red.jpg"),
      
      grmntn_lght_div_cd: null,
      grmntn_min_tmprtr_val: null,
      grmntn_max_tmprtr_val: null,
      sow_dt: null,
      sow_dt_asis: null,
      sow_dt_tobe: null,
      sowExist: false, 
      days_since_sow: null,
      sowDtEdit: false, 
      lght_div_cd: null,
      grwng_min_tmprtr_val: null,
      grwng_max_tmprtr_val: null,
      
      cmplntNm: null,

      days_since_water: null,


      resultArr: [],
      // cltvtAct: [{id:0, cltvtActCd:'100', cltvtActNm:'물주기'}, {id:1, name:'물주기'}, {id:2, name:'비료주기'}, {id:3, name:'해충박멸'}],
      cltvtAct: [
        { id: 0, cltvtActCd: "100", cltvtActNm: "물주기" },
        { id: 1, cltvtActCd: "200", cltvtActNm: "비료주기" },
        { id: 2, cltvtActCd: "300", cltvtActNm: "해충박멸" },
        { id: 3, cltvtActCd: "400", cltvtActNm: "분갈이" },
        { id: 4, cltvtActCd: "900", cltvtActNm: "기타메모" },
      ],
      // wikiSearchUrl,

      iframeUpdate: false,
      arrMemo: [],
      myPlantArr: null,
      myPlantRecordArr,
      myRecordsArr,
      inputUsrRecordArr: [],
      
      // style

    };
  },
  async created() {
    // get cltvtid that user set representation plant...
    // this.기본작물
    if (this.cltvtId === null) {
      this.cltvtId = 1;   ///////////// this.기본작물
    } 
    this.usrId = 1; /////////////////
    this.initialize(this.usrId, this.cltvtId);

  },
  beforeMount() {
  },
  mounted() {
  },
  beforeUpdate() {
  },
  methods: {
    /**
     * Scrolls to the passed section id if the section exists and the delay is over
     */
    // scrollToSection(id, force = false) {
    //   setTimeout(() => {
    //     this.inMove = false;
    //   }, this.inMoveDelay);
      
    // },
    async initialize(usrId, cltvtId) {
      await this.getMyPlant(usrId, cltvtId);
      await this.$refs.getMyPlantImageCmpnt.getMyPlantDiary(cltvtId, 0);
      await this.getUserRecords(usrId, cltvtId);
      
      this.usrId = usrId;
      this.cltvtId = cltvtId;
    },
    // setModalVal () {
    //   this.$refs.getMyPlantImageCmpnt.showDelImg = true;
    // },
    setMyPlantOpen(modalGbn){
      if(modalGbn == 'A') {
        // this.popupView = (this.popupView) ? false : true
        axios
          .post("/api/crop", {})
          // axios.post(this.router, resultJsonImp)
          .then((response) => {
            this.cropJson = response.data;
            // console.log('cropJson ++++ ', this.cropJson);
          })
          .catch((error) => {
          console.log(error);
          });
          
        // this.showModal = true;
        this.$refs.child_ref.setVal('showModal');
      }
      else if(modalGbn == 'B') {
        // this.regRecordModal = true;
        this.$refs.child_ref.setVal('regRecordModal');
      }
      else if(modalGbn == 'C') {
        // this.regDiaryModal = true;
        this.$refs.child_ref.setVal('regDiaryModal');
      }
      else if(modalGbn == 'D') {
        // this.regQuestionModal = true;
        this.$refs.child_ref.setVal('regQuestionModal');
      }
      else if(modalGbn == 'E') {
        // this.regRecordModal = true;
        this.$refs.child_ref.setVal('editProfileModal');
      }
    },  
    refreshMyPlantList(usrId, cltvtId) {
      // alert('refreshMyPlantList');
      this.getMyPlant(usrId, cltvtId);  // todo : this.cltvtId => userDefaultCltvtId
    },
    refreshRecord(usrId, cltvtId) {
      // console.log('refreshRecord usrId : ', usrId);
      // console.log('refreshRecord cltvtId : ', cltvtId);
      this.getUserRecords(usrId, cltvtId);
    }, 
    async refreshDiary(usrId, cltvtId) {
      await this.$refs.getMyPlantImageCmpnt.getMyPlantDiary(cltvtId);
    },
    async refreshProfile(usrId, cltvtId) {
      await this.getMyPlant(usrId, cltvtId);  // todo : this.cltvtId => userDefaultCltvtId
    }, 
    async getMyPlant(usrId, cltvtId) {
      
      console.log('getMyPlant :::>>> ', usrId, ' ', cltvtId);
      // const axios = require("axios");
      await axios
        .post("/api/plant", { contentsId: 15, usrId: usrId })
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          var myPlantJson;

          myPlantJson = response.data;
          this.myPlantArr = myPlantJson[0].get_result_json;
          // this.myPlantKey = Object.keys(this.myPlantArr[0]);
          // console.log('this.myPlantArr :::::: ', this.myPlantArr);
          for (var i in this.myPlantArr) {
            // console.log(this.myPlantArr[i].cltvt_id == cltvtId)
            if (this.myPlantArr[i].cltvt_id == cltvtId) {
              this.cmplntNm = this.myPlantArr[i].cmplnt_nm;
              this.grmntn_lght_div_cd = this.myPlantArr[i].grmntn_lght_div_cd;
              this.lght_div_cd = this.myPlantArr[i].lght_div_cd;
              this.grmntn_min_tmprtr_val = this.myPlantArr[i].grmntn_min_tmprtr_val;
              this.grmntn_max_tmprtr_val = this.myPlantArr[i].grmntn_max_tmprtr_val;
              this.sow_dt = this.myPlantArr[i].sow_dt;
              this.days_since_sow = this.myPlantArr[i].days_since_sow;
              this.grwng_min_tmprtr_val = this.myPlantArr[i].grwng_min_tmprtr_val;
              this.grwng_max_tmprtr_val = this.myPlantArr[i].grwng_max_tmprtr_val;
              this.cltvtId = this.myPlantArr[i].cltvt_id;
              this.days_since_water = this.myPlantArr[i].days_since_water;
              this.record_cnt = this.myPlantArr[i].record_cnt;

              // this.file_url = require("@/assets/images/" + this.myPlantArr[i].file_url);
              // this.file_url = require("~/" + usrId + "/" + "effa00de879308ac5318d8f3bdafb086.jpg");

              // this.file_url = "C:/dev/git/vue/cdn/myplant/1/" + this.myPlantArr[i].file_url;
              
              // this.urlRef = this.myPlantArr[i].ref_url;
              // this.wikiSearchUrl = wikiSearchUrl + this.item_name;
              // console.log('this.days_since_water ::: ', this.days_since_water);

              this.sow_dt_asis = this.sow_dt;
              if(this.sow_dt_asis != null) {
                sowExist = true;
              }
            }
          }
          

        })
        .catch((error) => {
          console.log(error);
        });
        
        await this.getMyPlantProfileImage(cltvtId);
    },
    async getMyPlantProfileImage(cltvtId) {
      const config = {
            method: "POST",
            url: "/api/plantShowImage",
            responseType: "json",
            data: { contentsId: 24, cltvtId: cltvtId },
        };

      var file; 
      var respJson;
      var buffer;

      axios(config)
        .then((response) => {
          respJson = response.data;

          file = respJson[0].file;

          ///// base64 -> ArrayBuffer -> Blob -> ObjectUrl
          // 1. get buffer(file obj)
          buffer = file.data;  // filesArr :  {type: 'Buffer', data: Array(515059)}
          
          // 2. buffer to blob
          const blob = new Blob([new Uint8Array(buffer)]);

          // 3. blob -> ObjectUrl
          const fileObjectUrl = window.URL.createObjectURL(blob); //revokeObjectURL???

          this.profileImg = {
                              cltvt_id:respJson[0].cltvt_id
                              , fileObjectUrl:fileObjectUrl};
          this.file_url = this.profileImg.fileObjectUrl;
          
        })
        .catch((error) => {
          console.log(error);
        });
        // this.file_url = require("~/" + usrId + "/" + "effa00de879308ac5318d8f3bdafb086.jpg");
    }, 
    getUserRecords(usrId, cltvtId) {
      var myPlantRecordJson;
      var recrdDttm; 
      var cltvtActCd;
      var cntnts;
      this.myRecordsArr = null;
      console.log('usrId : ', usrId, ' cltvtId : ', cltvtId);

      axios
        .post("/api/plant", { contentsId: 16, usrId: usrId, cltvtId: cltvtId })
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          myPlantRecordJson = response.data;
          this.myPlantRecordArr = myPlantRecordJson[0].get_result_json;
          var myRecordsJsonTmp = {};
          var myRecordsJsonArrayTmp = [];
          for (var i in this.myPlantRecordArr) {
            if (this.myPlantRecordArr[i].cltvt_id == cltvtId) {
              recrdDttm = this.myPlantRecordArr[i].recrd_dttm;
              cltvtActCd = this.myPlantRecordArr[i].cltvt_act_cd;
              cntnts = this.myPlantRecordArr[i].cntnts;

              var myRecordsJsonTmp = {
                recrdDttm: recrdDttm,
                cltvtActCd: cltvtActCd,
                cntnts: cntnts,
              };
              myRecordsJsonArrayTmp.push(myRecordsJsonTmp);
              this.myRecordsArr = myRecordsJsonArrayTmp;
            }
          }
          // console.log('this.myRecords length :::: ', this.myRecords.length);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async getMyPlantSelected(usrId, cltvtId) {
      await this.getMyPlant(usrId, cltvtId);
      await this.$refs.getMyPlantImageCmpnt.getMyPlantDiary(cltvtId);
      await this.getUserRecords(usrId, cltvtId);
    },
    editSowDtEdit(){
      axios
        .post("/api/cultivate", {dml: 'update', sow_dt: this.sow_dt.replace('-', '').replace('-', ''), cltvt_id: this.cltvtId})
        .then((response) => {
          this.sowDtEdit = false;
          this.getMyPlant(this.usrId, this.cltvtId);
        })
        .catch((error) => {
        console.log(error);
        });
      // console.log('this.sow_dt : ', this.sow_dt, this.sow_dt.replace('-', '').replace('-', ''));
    },
