<template>
  <div class="contents_div_1">
    <h2>공지사항</h2>
    <h5>
      listMode : {{this.listMode}} / viewMode : {{this.viewMode}} </h5>
    
    <!-- 조회form -->
    <div class="container-column" v-if="viewMode">
      <input type="button" class="button-board" value="목록으로" v-on:click="this.viewMode=false; this.listMode=true;" />
      <input type="text" class="input-text" readonly="readonly" v-model="this.title" />
      <textarea class="textarea-basic" cols="30" rows="20" readonly="readonly" v-model="this.contents" />
    </div>

    <div class="row">
      <table>
        <thead>
          <tr>
            <th v-for="(a, i) in resultKey" :key="i">{{ this.resultKey[i] }}</th>
          </tr>
        </thead>
        <tbody>
          <!-- <tr v-for="(row,rid) in resultArr" :key="rid" v-on:click="viewData({{row.heal_act_id}});" style="cursor:pointer;"> -->
          <tr
            v-for="(row, rid) in resultArr"
            :key="rid"
            v-on:click="viewData(row.heal_act_id)"
            style="cursor: pointer"
          >
            <td v-for="(col, cid) in row" :key="cid" v-text="col"></td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="row">...페이지...</div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      inputTag: "",
      tagId: "0",
      listMode: true,
      viewMode: false,
      title: "",
      contents: "",
      resultArr: [],
      resultKey: [],
    };
  },
  created() {
    this.contentsList('공지');
  },
  mounted() {
    // console.log(this.img);
  },
  methods: {
    contentsList(tagName) {
      this.listMode = true;
      this.viewMode = false;
      
      const axios = require("axios");
      axios
        // .post("/api/healActBoard", { dml: '', contentsId: "9" })
        .post("/api/healActBoard", { dml: '', contentsId: 13, tag: tagName })
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          this.resultJson = response.data;
          this.resultArr = this.resultJson[0].get_result_json;
          this.resultKey = Object.keys(this.resultArr[0]);
          // delete this.resultKey[2];
        })
        .catch((error) => {
          console.log(error);
        });
      this.healActId = null;
    },
    viewData(healActId) {
      this.listMode = false;
      this.viewMode = true;

      var resultJson;
      var resultArr;
      var resultKey;

      const axios = require("axios");
      axios
        .post("/api/healActBoard", { dml: '', healActId: healActId })
        .then((response) => {
          resultJson = response.data;
          resultArr = resultJson[0].get_result_json;
          // resultKey = Object.keys(resultArr[0]);
          
          this.title = resultArr[0].title;
          this.contents = resultArr[0].contents;
          this.healActId = healActId;
          // console.log('this.title = ', this.title);
        })
        .catch((error) => {
          console.log(error);
        });
        this.listMode=false;
    },    
  },
};
</script>

<style>
/* @import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap"); */
</style>
